import React, { useState, useEffect, useRef } from "react";

import Logs from "./cw-activitylog.transcript";

const moment = require("moment");

function CwEventLog(props) {
  let TimerClock = 0;
  const [activitySummary, SetActivitySummary] = useState([]);

  const list = useRef();

  const ActivitySummaryTimer = () => {
    TimerClock = TimerClock + 1;

    const arrValue = Logs.transcript[TimerClock];

    if (arrValue != null) {
      let item = arrValue;
      item.timeStamp = moment().format("HH:MM:SS");
      SetActivitySummary((activitySummary) => [...activitySummary, arrValue]);
    }

    if (Logs.activityEnds >= TimerClock) {
      console.log("logTimer", TimerClock);
      ActivitySummaryTimer();
    }
  };

  useEffect(() => {
    ActivitySummaryTimer();
  }, []);

  const ListOfActivitySummary = (props) => {
    return activitySummary.map((item) => {
      return (
        <div className={"padding-bottom-20"}>
          <div className={"padding-left-50 padding-right-50 row"}>
            <div className="time-stamp">
              <span>[{item.timeStamp}]</span>
            </div>
            <div className="message-log">
              <span>{item.message}</span>
            </div>
          </div>
        </div>
      );
    });
  };

  return (
    <div id="ListOfLogs" className="row justify-content-center">
      <ListOfActivitySummary />
    </div>
  );
}

export default CwEventLog;
