import React from "react";
import Video from "./video";
import RapidDeployVideo from "../Assets/Flood-CCTV-videos.mp4";

export default function RapidDeployCCTV() {
  return (
    <Video
      vid={RapidDeployVideo}
      width={1920}
      height={1071}
      loop={true}
      muted={true}
    />
  );
}
