import React from "react";

import { Row } from "react-bootstrap";

const SummaryArrivedInfo = (props) => {
  return (
    <div className={"date-info margin-top-20 padding-left-40"}>
      <Row>
        <div className={"span-text"}>{props.arrivedInfo}</div>
      </Row>
    </div>
  );
};
export default SummaryArrivedInfo;
