import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import IncidentHeader from "../Components/IncidentHeader";
import DeploymentBar from "../Components/deployment";
import ActivityLog from "../Components/ActivityLogCard";
import CCTVCard from "../Components/CCTV";
import DroneCapture from "../Components/DroneCaptureCard";
import {
  PredictedContent,
  PredictedThreatsCard,
} from "../Components/PredictedThreatsCard";
import { WeatherDataCard } from "../Components/WeatherDataCard";
import { SensorDataCard } from "../Components/SensorDataCard";

import { RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";

import ScreenControl from "../screencontrol";
import Constants from "../Constants";

function Separator() {
  return <div style={{ paddingTop: "20px" }} />;
}

function Threats() {
  const [progress, setProgress] = useState(300);
  if (progress < 200) {
    setTimeout(() => {
      setProgress(progress + 0.6);
    }, 6);
  }
  const [accepted, setAccepted] = useState(false);
  if (accepted === true) {
    setTimeout(() => {
      setAccepted(false);
    }, 1000);
  }

  const location = useLocation();
  const [rightScreen, setRightScreen] = useRecoilState(RightScreenState);
  if (location.pathname === "/incident-threats") {
    ScreenControl(Constants.videoType.c4News);
    setRightScreen(Constants.videoType.c4Incident);
  }

  return (
    <div>
      <IncidentHeader />
      <DeploymentBar launchname="NEXT" launchroute="/incident-response" />
      <Separator />
      <div className={"container-dashboard"}>
        <Row>
          <Col className="col-3 padding-bottom-20">
            <ActivityLog repeat={false} />
            {/* each card container space */}
            <Separator />
            <WeatherDataCard />
            {/* each card container space */}
            <div style={{ paddingTop: "23px" }} />
            <SensorDataCard />
          </Col>
          <Col className="col-6">
            <div className="threats-map" />
            <div style={{ paddingTop: "20px" }} />
            <PredictedThreatsCard
              accepted={accepted}
              onClick={() => {
                setAccepted(!accepted);
              }}
            />
          </Col>
          <Col className="col-3">
            {CCTVCard(progress)}
            <div style={{ paddingTop: "20px" }} />
            <DroneCapture />
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default Threats;
