import React from "react";
import { Row, Col } from "react-bootstrap";

const SummaryCaller = (props) => {
  return (
    <div className={"caller-info margin-top-10"}>
      <Row>
        <Col lg={4} className={"padding-left-40"}>
          <div className={"text"}>Caller</div>
        </Col>
        <Col lg={8}>
          <div className={"row"}>
            <span className={"span-text"}>{props.CallerId}</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SummaryCaller;
