import React from "react";
import {
  HashRouter as Router,
  Switch,
  Route,
  Link,
  useHistory,
  useLocation,
} from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import FloatingMenu from "./Components/floatingMenu";
import LeftScreen from "./Components/LeftScreen";
import RightScreen from "./Components/RightScreen";
import Pages from "./Pages";

function Routes() {
  return (
    <Router>
      <div className="d-flex">
        <div className="container-platform">
          <Switch>
            <Route exact path="/">
              {" "}
              <Pages.Home />{" "}
            </Route>
            <Route exact path="/incident-response">
              {" "}
              <Pages.IncidentResponse />{" "}
            </Route>
            <Route exact path="/incident-report">
              {" "}
              <Pages.IncidentOverview />{" "}
            </Route>
            <Route exact path="/incident-threats">
              {" "}
              <Pages.Threats />{" "}
            </Route>
            <Route exact path="/citizen-watch">
              {" "}
              <Pages.CitizenWatch />{" "}
            </Route>
          </Switch>

          <FloatingMenu />
        </div>

        <RightScreen />
      </div>
    </Router>
  );
}

export function HomeButton(props) {
  let history = useHistory();

  function handleClick() {
    history.push("/");
  }

  return (
    <button type="button" className={props.className} onClick={handleClick} />
  );
}

export function BackButton(props) {
  let history = useHistory();
  return (
    <button className={props.className} onClick={() => history.goBack()} />
  );
}

export function NextButton(props) {
  let history = useHistory();
  let location = useLocation();

  function handleClick() {
    if (location.pathname === "/incident-report") {
      history.push("/incident-threats");
    } else if (location.pathname === "/incident-threats") {
      history.push("/incident-response");
    } else if (location.pathname === "/") {
      history.push("/incident-report");
    } else {
      history.push("/");
    }
  }

  return (
    <button type="button" className={props.className} onClick={handleClick}>
      <div>NEXT</div>
    </button>
  );
}

