import React from "react";
import { Row, Col } from "react-bootstrap";

const SubmitedIncidentDetails = (props) => {
  return (
    <div className={"incident-info margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Incident details</div>
        </Col>
        <Col lg={9}>
          <div className={"row text-box-submit"}>
            <span className={"span-text"}>{props.incidentDetails}</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SubmitedIncidentDetails;
