import React from "react";
import { Col, Row } from "react-bootstrap";
import Clock from "react-live-clock";

function CitizenRespondersHeader() {
  return (
    <div class="cw-header">
      <div class="liner">
        <span className={"cw-text title"}> Community Responders Console </span>{" "}
        <span className={"cw-text value"}>
          {" "}
          Singapore 760825, Blk 825 Yishun Street 81{" "}
        </span>
        <Clock
          className="clock"
          format={"dddd, MMMM DD, YYYY, HH:mm"}
          ticking={true}
          timezone={"Asia/Singapore"}
        />
      </div>
    </div>
  );
}

export default CitizenRespondersHeader;
