import React from "react";
import { Row, Col, Button } from "react-bootstrap";

const SelectIncident = (props) => {
  return (
    <div className={"select-incident margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Select incident:</div>
        </Col>
        <Col lg={9}>
          <div className={"row incident-button"}>
            <Button
              value="Medical Emergency"
              name="incidentbutton"
              onClick={props.handleChange}
              className={
                props.buttonChange.incidentbutton === "Medical Emergency"
                  ? "btn-incident-active"
                  : "btn-incident"
              }
            >
              Medical Emergency
            </Button>
            <Button
              value="Minor Fire"
              name="incidentbutton"
              onClick={props.handleChange}
              className={
                props.buttonChange.incidentbutton === "Minor Fire"
                  ? "btn-incident-active"
                  : "btn-incident"
              }
            >
              Minor Fire
            </Button>
            <Button
              value="Major Incident"
              name="incidentbutton"
              onClick={props.handleChange}
              className={
                props.buttonChange.incidentbutton === "Major Incident"
                  ? "btn-incident-active"
                  : "btn-incident"
              }
            >
              Major Incident
            </Button>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SelectIncident;
