import React, { useState } from "react";

import { Row, Modal, Button } from "react-bootstrap";

const CRPSteps = () => {
  return (
    <Row className={"margin-0 margin-top-10 padding-left-70 padding-right-70"}>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>1.</span>{" "}
        <span className={"span-text"}>
          Quickly assess the situation for danger, so that the rescuer can
          operate in a safe environment.
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>2.</span>{" "}
        <span className={"span-text"}>
          Roll the casualty over onto his/her back. Do take care that the head,
          neck and body are supported and turned simultaneously during
          repositioning
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>3.</span>{" "}
        <span className={"span-text"}>
          Expose the chest adequately and start chest compression over the lower
          half of the sternum (breastbone).
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>4.</span>{" "}
        <span className={"span-text"}>
          Place the heel of your hand on the lower half of the casualtys
          sternum (breastbone)
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>5.</span>{" "}
        <span className={"span-text"}>
          Interface the fingers of both hands and lift the fingers off the chest
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>6.</span>{" "}
        <span className={"span-text"}>
          Straighten both elbows and lock them in position. Position your
          shoulders directly over the casualtys chest.
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>7.</span>{" "}
        <span className={"span-text"}>
          Use your body weight to compress the casualtys chest vertically to a
          depth of 4-6 cm, counting as you compress.
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>8.</span>{" "}
        <span className={"span-text"}>
          30 chest compressions at a rate of 100-120 per minute.
        </span>
      </div>
      <div className={"crp-text margin-top-10"}>
        <span className={"step span-text"}>Step</span>
        <span className={"span-text"}>9.</span>{" "}
        <span className={"span-text"}>
          Perform mouth-to-mouth ventilations using a Head-Tilt, Chin-Lift
          maneuver.
        </span>
      </div>
    </Row>
  );
};
export default CRPSteps;
