import React from "react";
import { Row, Col } from "react-bootstrap";

const SummaryIncidentDetails = (props) => {
  return (
    <div className={"incident-info margin-top-20"}>
      <Row>
        <Col lg={4} className={"padding-left-40"}>
          <div className={"text"}>Details</div>
        </Col>
        <Col lg={8}>
          <div className={"row"}>
            <span className={"span-text"}>{props.incidentDetails}</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SummaryIncidentDetails;
