import React from "react";
import Card from "../Components/card";
import { Col } from "react-bootstrap";
export default function SituationOverview() {
  return (
    <Card>
      <div className="situation-overview padding-bottom-35">
        <h1 className="content-title">
          Situation Overview{" "}
          <span className={"severity"}>
            Severity: <span className={"red-text"}>VERY SEVERE</span>
          </span>
        </h1>

        <div className={"content-log row" + "margin-0"}>
          <div className="situation-overview-left col-4">
            <div className="row padding-top-10">
              <Col
                lg={5}
                className="px-0"
                // style={{ maxWidth: "120px", padding: "0" }}
              >
                <p className="situation-overview-desc">Major Incident</p>
              </Col>
              <Col
                lg={7}
                className="px-0"
                // style={{ maxWidth: "150px", padding: "0" }}
              >
                <p className="situation-overview-desc-text">
                  Declared at 14:38
                </p>
              </Col>
            </div>

            <div className="row padding-top-10">
              <Col
                lg={5}
                className="px-0"
                // style={{ maxWidth: "120px", padding: "0" }}
              >
                <p className="situation-overview-desc">Exact Location</p>
              </Col>
              <Col
                lg={6}
                className="px-0"
                // style={{ maxWidth: "150px", padding: "0" }}
              >
                <p className="situation-overview-desc-text">
                  250 North Bridge Rd., Singapore 179101
                </p>
              </Col>
            </div>

            <div className="row padding-top-10">
              <Col
                lg={5}
                className="px-0"
                // style={{ maxWidth: "120px", padding: "0" }}
              >
                <p className="situation-overview-desc">Type of Incident</p>
              </Col>
              <Col
                lg={5}
                className="px-0"
                // style={{ maxWidth: "150px", padding: "0" }}
              >
                <p className="situation-overview-desc-text">
                  {" "}
                  Commercial Building Fire
                </p>
              </Col>
            </div>
          </div>
          <div className="situation-overview-right col-4 padding-top-10">
            <div className="row">
              <Col
                lg={7}
                className="px-0"
                // style={{ maxWidth: "152px", padding: "0" }}
              >
                <p className="situation-overview-desc">Number of Casualties</p>
              </Col>
              <Col
                lg={4}
                className="px-0"
                // style={{ maxWidth: "104px", padding: "0" }}
              >
                <p className="situation-overview-desc-text float-right">
                  25 - <span className="red-text">HIGH</span>
                </p>
              </Col>
            </div>
            <div className="row padding-top-10">
              <p className="situation-overview-desc">Emergency Resources</p>
            </div>
            <div className="row">
              <Col
                lg={6}
                className="px-0"
                // style={{ maxWidth: "152px", padding: "0" }}
              >
                <div>
                  <span className="listType-left-default"></span>
                  <p className="situation-overview-desc-text margin-left-10">
                    <span>SPF Patrol Unit</span>
                  </p>
                </div>
                <div>
                  <span className="listType-left-default"></span>
                  <p className="situation-overview-desc-text margin-left-10">
                    SCDF EMS
                  </p>
                </div>
              </Col>
              <Col
                lg={6}
                // className="mx-5"
                // style={{ maxWidth: "104px", padding: "0" }}
              >
                <div>
                  <p className="situation-overview-desc-text float-right">
                    400m away
                  </p>
                </div>
                <div>
                  <p className="situation-overview-desc-text float-right">
                    Not Deployed
                  </p>
                </div>
              </Col>
            </div>
          </div>

          <div className="situation-overview-right col-4">
            <div className="row padding-top-10">
              <p className="situation-overview-desc">
                Hazards Present<span className="red-text">HIGH</span>
              </p>
              <div>
                <span className="listType-left-default"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  Limited Lift Access
                </p>
              </div>
              <div>
                <span className="listType-left-default"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  Large Number of People
                </p>
              </div>
              <div>
                <span className="listType-left-default"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  High Smoke Density
                </p>
              </div>
            </div>

            <div className="row padding-top-10">
              <p className="situation-overview-desc">Access and Egress</p>
              <div>
                <span className="listType-left-default"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  Access from Stamford Rd.
                </p>
              </div>
              <div>
                <span className="listType-left-warning animate__animated animate__flash animate__infinite	infinite"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  Congestion along 90 Bras Barah Rd.
                </p>
              </div>
              <div>
                <span className="listType-left-warning animate__animated animate__flash animate__infinite	infinite"></span>
                <p className="situation-overview-desc-text margin-left-10">
                  Road works along at 9 Victoria St.
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Card>
  );
}
