import React, { useState } from "react";
import { Button } from "react-bootstrap";

// import SubmitedCaller from "./form-submit/submittedCaller";
// import SubmitedLocation from "./submittedLocation";
// import SubmitedIncidentDetails from "./submittedIncidentDetails";
// import SelectedIncident from "./SelectedIncident";
// import SelectedCase from "./SelectedCase";
// import SelectedRate from "./form-submit/SelectedRate";

import Form from "./form-submit";

const SubmittedForm = (props) => {
  const data = props.data;
  const edit = props.editButton;

  return (
    <div className={"header-info"}>
      <Button type="button" onClick={edit} className={"edit-button"}>
        <span className={"edit-pen"}></span>
        Edit Details
      </Button>
      <Form.SubmitedCaller CallerId={data.callerId} />
      <Form.SubmitedLocation location={data.location} street={data.street} />
      <Form.SubmitedIncidentDetails incidentDetails={data.incidentDetails} />
      <Form.SelectedIncident buttonSelected={data.incidentbutton} />
      <Form.SelectedCase buttonSelected={data.casebutton} />
      <Form.SelectedRate buttonSelected={data.ratebutton} />
    </div>
  );
};
export default SubmittedForm;
