import React, { useState } from "react";
import { Row, Col, Button } from "react-bootstrap";
import Card from "../Components/card";

import Form from "./form-submit";

import SubmittedForm from "./submittedForm";
import CRPForm from "./crpSop";
import LoadingSubmit from "./submitLoading";

function CitizenOverview(props) {
  const [values, setValues] = useState({
    callerId: "8111 1111",
    location: "Singapore 760825, Blk 825 Yishun Street 81",
    street: "",
    incidentDetails: "",
    incidentbutton: "",
    casebutton: "",
    ratebutton: "",
  });
  console.log(values);
  const [clicked, setClicked] = useState(false);
  const [loading, setLoading] = useState(false);
  function onEdit() {
    setClicked(false);
    setTimeout(() => {
      setLoading(false);
    }, 1500);
  }

  const [toggleState, setToggleState] = useState(1);
  let setCheck = false;
  const toggleTab = (index) => {
    setToggleState(index);
    setCheck = false;
  };

  const handleChange = (event) => {
    if (event.target.value === "Cardiac Arrest") {
      props.doCallBack(event.target.value);
      console.log("pressed cardiac arrest");
    }
    if (event.target.value === "Life-threatening") {
      props.doCallBack(event.target.value);
      console.log("pressed Life-threatening");
    }
    setValues({ ...values, [event.target.name]: event.target.value });
  };

  const handleSubmit = (event) => {
    console.log("form submited: " + JSON.stringify(values));
    setClicked(true);
    setTimeout(() => {
      setLoading(true);
    }, 1500);
    event.preventDefault();
    setToggleState(1);
    props.assetMap(true);
    props.doCallBack("RequestForEMS-Submmited");
  };

  // const FormClear = () => {
  //   setClicked(false);
  //   console.log("form clear");
  // };
  // const onHandleClear = (event) => {
  //   setValues(event);
  //   FormClear();
  //   console.log(event);
  // };
  const onEndCall = () => {
    props.endCall(true);
  };
  const setText = () => {
    setValues({
      callerId: "8111 1111",
      location: "Singapore 760825, Blk 825 Yishun Street 81",
      street: "Level 1, Staircase D, near lift",
      incidentDetails:
        "Man found lying on the floor unconscious, suspected cardiac arrest.",
      incidentbutton: "",
      casebutton: "",
      ratebutton: "",
    });
  };
  if (
    values.ratebutton === "" ||
    values.casebutton === "" ||
    values.incidentbutton === ""
  ) {
    //console.log("no");
    setCheck = false;
  } else {
    //console.log("yes");
    setCheck = true;
  }

  if (clicked === false) {
    return (
      // Form Content
      <Card>
        <div className="citizen-overview">
          <h1 className="content-title">Situational Overview</h1>
          <div className={"header-info scrollbar"}>
            <form onSubmit={handleSubmit}>
              <Form.Caller CallerId={values.callerId} />

              <Form.Location
                location={values.location}
                street={values.street}
                handleChange={handleChange}
              />

              <Form.IncidentDetails
                incidentDetails={values.incidentDetails}
                handleChange={handleChange}
                setText={setText}
              />

              <Form.SelectIncident
                handleChange={handleChange}
                buttonChange={values}
              />

              <Form.SelectCase
                handleChange={handleChange}
                buttonChange={values}
              />

              <Form.SelectRate
                handleChange={handleChange}
                buttonChange={values}
              />

              <Row className={"justify-content-center"}>
                <div className={"margin-top-50 margin-bottom-50"}>
                  {/* {setCheck == false && (
                    <Button
                      type="submit"
                      className={"request-button button-disabled"}
                    >
                      Request for EMS
                    </Button>
                  )}
                  {setCheck == true && ( */}
                  <Button type="submit" className={"request-submitted"}>
                    Request for EMS
                  </Button>
                  {/* )} */}
                </div>
              </Row>
            </form>
          </div>
        </div>
      </Card>
    );
  } else {
    return (
      // Submitted Form Content
      <Card>
        <div className="citizen-overview">
          {loading === false ? (
            // loading submitted form
            <LoadingSubmit />
          ) : (
            // after loading submitted
            <div>
              {props.switchToogle == false ? (
                <Row className="bloc-tabs">
                  <Col
                    lg={6}
                    onClick={() => toggleTab(1)}
                    className={toggleState === 1 ? "tabs active-tabs" : "tabs"}
                  >
                    <div className="tab-content-title">
                      Situational Overview
                    </div>
                  </Col>

                  <Col
                    lg={6}
                    onClick={() => toggleTab(2)}
                    className={toggleState === 2 ? "tabs active-tabs" : "tabs"}
                  >
                    <div className="tab-content-title">CPR SOP</div>
                  </Col>
                </Row>
              ) : (
                <Row className="bloc-tabs">
                  <Col
                    lg={6}
                    onClick={() => toggleTab(1)}
                    className={toggleState === 1 ? "tabs" : "tabs"}
                  >
                    <div className="tab-content-title">
                      Situational Overview
                    </div>
                  </Col>

                  <Col
                    lg={6}
                    onClick={() => toggleTab(2)}
                    className={
                      toggleState === 2 || props.switchToogle == true
                        ? "tabs active-tabs"
                        : "tabs"
                    }
                  >
                    <div className="tab-content-title">CPR SOP</div>
                  </Col>
                </Row>
              )}
              <div className="content-tabs">
                {props.switchToogle == false ? (
                  <div>
                    {toggleState === 1 && (
                      <SubmittedForm
                        editButton={() => onEdit()}
                        data={values}
                      />
                    )}
                    {toggleState === 2 && (
                      <CRPForm
                        // onHandleClear={(event) => {
                        //   onHandleClear(event);
                        // }}
                        endCall={() => onEndCall()}
                        editButton={() => onEdit()}
                        data={values}
                      />
                    )}
                  </div>
                ) : (
                  <CRPForm
                    // onHandleClear={(event) => {
                    //   onHandleClear(event);
                    // }}
                    endCall={() => onEndCall()}
                    editButton={() => onEdit()}
                    data={values}
                  />
                )}
              </div>
            </div>
          )}
        </div>
      </Card>
    );
  }
}

export default CitizenOverview;
