import React from "react";
import { Row, Col } from "react-bootstrap";

const Caller = (props) => {
  return (
    <div className={"caller-info margin-top-10"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Caller</div>
        </Col>
        <Col lg={9}>
          <div className={"row text-box-sm"}>
            <span className={"span-text"}>{props.CallerId}</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default Caller;
