import {Col, Row} from "react-bootstrap";
import React from "react";
import Card from "./card";
import {Link} from "react-router-dom";

export function PredictedContent(props) {

    return <div className={"predicted-content"}>
        <Row>
            <Col>
                <Row className={"entry"}
                     style={props.accepted === true ? {opacity: "1"} : {opacity: "1"}}>
                    <div className={'red-text'} >{"\u2022"}</div>
                    <div>{"\u2022"}</div>
                    <div className={'big-white-text'}>1 potential threat predicted</div>
                </Row>
                <Row className={"entry"}
                     style={props.accepted === true ? {opacity: "1"} : {opacity: "1"}}>
                    <div className={"white-text"}>Potential spread of toxic fumes within 300m radius north-west of
                        incident site.
                    </div>
                </Row>
                <Row className={"button-row"}
                     style={props.accepted === true ? {opacity: "1"} : {opacity: "1"}}>
                </Row>
            </Col>
            <Col>
                <Row className={"entry"}
                     style={props.accepted === true ? {opacity: "1"} : {opacity: "1"}}>
                    <div className={"white-text"}>Impact of Threat</div>
                    <div className={"grey-text"}>{"\u2022"} Primary evacuation zone is within predicted radius of toxic
                        fumes.
                    </div>
                    <div className={"grey-text"}>{"\u2022"} Initial cordon around incident site is within predicted
                        radius of toxic fumes.
                    </div>
                </Row>
                <Row className={'button-row'}>
                    <div>
                    </div>
                </Row>
            </Col>
        </Row>
        <div className={'button-row'}>
            <button
                className="run-simulation-button"
            >RUN SIMULATION
            </button>
            <Link to="/incident-response">
            <button
                className={props.accepted === true ? "accepted-text" : "situation-overview-button"}
                onClick={props.onClick}>VIEW SOP
            </button>
            </Link>
        </div>
    </div>;
}

export function PredictedThreatsCard(props) {
    return <Card>
        <div
            className="situation-overview"
            style={{paddingBottom: "35px"}}
        >
            <h1 className="content-title">Predicted Threats</h1>
            <PredictedContent accepted={props.accepted} onClick={props.onClick}/>
        </div>
    </Card>;
}
