import React from "react";
import Card from "../Components/card";

export default function LocationData() {
  return (
    <Card>
      <div className="location-data">
        <h1 className="content-title">Location Data</h1>
        <div className="location-content-log ">
          <div className="location-data-content">
            <p className="location-data-content-1">Building</p>
            <p className="location-data-content-2">Raffles City Tower</p>
          </div>

          <div className="location-data-content">
            <p className="location-data-content-1">Address</p>
            <p className="location-data-content-2">
              250 North Bridge Rd, Singapore 179101
            </p>
          </div>

          <div className="location-data-content">
            <p className="location-data-content-1">Registered Occupancy</p>
            <p className="location-data-content-2">
              44 Offices, 3250 Occupants
            </p>
          </div>

          <div className="location-overview-left">
            <div className="padding-top-10">
              <p className="location-overview-desc">Fire Lifts</p>
              <ul className="location-overview-1 text-left">
                <li>Lobby A, Lift A (Odd Floors)</li>
                <li>Lobby B, Lift B (Even Floors)</li>
              </ul>
            </div>
          </div>
          <div className="location-overview-left">
            <div className="padding-top-10">
              <p className="location-overview-desc">
                Fire Suppression Systems Available
              </p>
              <p className="location-overview-desc-text">
                Sprinklers, HDG Tank (Rooftop)
              </p>
            </div>
          </div>
          <div className="location-overview-left">
            <div className="padding-top-10">
              <p className="location-overview-desc">Key Personnel</p>
              <p className="location-overview-desc-text">
                Tan Hock Yew, Site Main Controller (SMC) 91112111
              </p>
            </div>
          </div>
        </div>
      </div>
    </Card>
  );
}
