import React from "react";
import Constants from "../Constants";

function ProgressBar(props) {
  const percent = props.percent;
  const data = Constants.progressBarType;

  let barColor;

  if (percent <= 25) {
    barColor = data.success;
  } else if (percent >= 25 && percent <= 70) {
    barColor = data.info;
  } else if (percent >= 71 && percent <= 90) {
    barColor = data.warning;
  } else {
    barColor = data.danger;
  }

  return (
    <div className="progress-wd">
      <div
        className="bar"
        style={{ width: percent + "%", backgroundColor: barColor }}
      />
      {/* <div className="text">{percent}%</div> */}
    </div>
  );
}

export default ProgressBar;
