import SwiperCore, {
  Navigation,
  Pagination,
  Scrollbar,
  A11y,
  Autoplay,
} from "swiper";
import React from "react";
// Import Swiper styles
import "swiper/swiper.scss";
import "swiper/components/navigation/navigation.scss";
import "swiper/components/pagination/pagination.scss";
import "swiper/components/scrollbar/scrollbar.scss";

import WidgetValue from "./widget-value";
import { Row, Col } from "react-bootstrap";

// install Swiper modules
SwiperCore.use([Navigation, Pagination, Scrollbar, A11y, Autoplay]);

export default function Carousel(props) {
  const [weather, setWeather] = React.useState("CLOUDY");

  if (props.clicked) {
    if (weather === "CLOUDY") {
      setWeather("THUNDERSTORM");
    }
  }

  return (
    <Row>
      <Col className={"weather-forecast"}>
        <div className="stat-box">
          <div className="value small">{weather}</div>
          <div className="label">Weather Forecast</div>
        </div>
      </Col>

      <Col className={"temp"}>
        {props.clicked === true ? (
          <WidgetValue
            value={25}
            prefix={"C"}
            title={"Avg. Temp"}
            startvalue={30}
            duration={10}
            decimals={0}
            fontsize={"small"}
          />
        ) : (
          <WidgetValue
            value={30}
            prefix={"C"}
            title={"Avg. Temp"}
            startvalue={34}
            duration={10}
            decimals={0}
            fontsize={"small"}
          />
        )}
      </Col>

      <Col className={"uv-index"}>
        {props.clicked === true ? (
          <WidgetValue
            startvalue={2}
            duration={8}
            value={1}
            decimals={0}
            fontsize={"small"}
            prefix={" - LOW"}
            title={"UV Index"}
          />
        ) : (
          <WidgetValue
            startvalue={4}
            duration={8}
            value={6}
            decimals={0}
            fontsize={"small"}
            prefix={" - HIGH"}
            title={"UV Index"}
          />
        )}
      </Col>

      <Col className={"rain"}>
        <div className="stat-box">
          {props.clicked === true ? (
            <div className="value small rain-heavy">HEAVY</div>
          ) : (
            <div className="value small rain-low">LOW</div>
          )}
          <div className="label">Rain Intensity</div>
        </div>
      </Col>

      <Col className={"wind"}>
        {props.clicked === true ? (
          <WidgetValue
            value={35}
            sufix={"NW "}
            prefix={" KM/H"}
            title={"Wind"}
            startvalue={2}
            duration={12}
            decimals={0}
            fontsize={"small"}
            double={true}
            midfix={"-"}
            startvalue2={3}
            value2={40}
            duration2={6}
            decimals2={0}
          />
        ) : (
          <WidgetValue
            value={2}
            sufix={"NW "}
            prefix={" KM/H"}
            title={"Wind"}
            startvalue={1}
            duration={12}
            decimals={0}
            fontsize={"small"}
            double={true}
            midfix={"-"}
            startvalue2={1}
            value2={3}
            duration2={6}
            decimals2={0}
          />
        )}
      </Col>
    </Row>
  );
}
