import React, { useState, useEffect, useRef } from "react";
import Card from "../card";
import Constants from "../../Constants";
import { CwActivityLogState } from "../../Atom";
import { useRecoilState } from "recoil";
import Logs from "./cw-activitylog.transcript";

const moment = require("moment");

function CwActivityLog(props) {
  const log = Constants.ActivityLogCW;

  const [activityList, SetActivitylist] = useState([]);

  const [timeFrame, setTimeFrame] = useState(0);
  const [localTimerPlaying, setLocalTimerPlaying] = useState(false);
  const [activityState, setActivityState] = useRecoilState(CwActivityLogState);

  const [logTimer, setLogTimer] = useState(0);
  const list = useRef();

  const timeFrameRef = useRef(timeFrame);
  timeFrameRef.current = timeFrame;

  const activityStateRef = useRef(activityState);
  activityStateRef.current = activityState;

  const ActivityTimer = () => {
    setTimeout(() => {
      console.log("tick", activityStateRef);

      if (activityStateRef.current == true) {
        setTimeFrame(timeFrameRef.current + 1);
        const timeSecond = timeFrameRef.current;
        console.log("timeSecond", timeSecond);

        if (Logs.transcript[timeSecond] != null) {
          if (Logs.transcript[timeSecond].event != "") {
            // callback
            console.log(
              "Logs.transcript[timeSecond].event",
              Logs.transcript[timeSecond].event
            );
            props.onEvent(Logs.transcript[timeSecond].event);
            //props.onSentiment(Logs.transcript[timeSecond].message);
          }
          let item = Logs.transcript[timeSecond];
          item.timeStamp = moment().format("HH:mm:ss");
          SetActivitylist((activityList) => [
            ...activityList,
            Logs.transcript[timeSecond],
          ]);

          if (
            Logs.transcript[timeSecond].event == Constants.ActivityType.PAUSE
          ) {
            setActivityState(false);
          }
          if (Logs.activityEnds >= timeSecond && activityState == true) {
            console.log("logTimer", timeSecond);
          }
          //props.onSentiment(Logs.transcript[timeSecond].message);
        }
      }

      ActivityTimer();
    }, 1000);
  };

  useEffect(() => {
    ActivityTimer();
  }, []);

  useEffect(() => {
    console.log("logTimer", logTimer);
  }, [logTimer]);

  useEffect(() => {
    const ListOfLogs = document.getElementById("ListOfLogs");
    ListOfLogs.scrollTop = ListOfLogs.scrollHeight;
  }, [activityList]);

  const ListOfActivity = (props) => {
    return activityList.map((item) => {
      return (
        <div className={"padding-bottom-20"}>
          <div>
            <span className="actor">{item.from}</span>{" "}
            <span className="timestamp padding-left-5">[{item.timeStamp}]</span>
          </div>
          <div className="message">{item.message}</div>
        </div>
      );
    });
  };

  return (
    <Card>
      <div class="activity-widget">
        <h1 class="content-title">Call Log</h1>

        <div
          id="ListOfLogs"
          className={
            props.videoCall === true
              ? "container-height container"
              : "container"
          }
        >
          <ListOfActivity data={Constants.ActivityLogCW} />
        </div>
      </div>
    </Card>
  );
}

export default CwActivityLog;
