import React, { useState } from "react";
import { Row, Col } from "react-bootstrap";
import Header from "../Components/header";
import NEA_WeatherTracking from "../Components/NEA_WeatherTracking";
import { Link, useHistory } from "react-router-dom";
import { LeftScreenState, RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";

import ScreenControl from "../screencontrol";
import {
  InitialIncidentData,
  IncomingIncidentData,
  IncomingMapData,
} from "../Components/IncidentData";

import Constants from "../Constants";

function Home() {
  //const [leftScreen, setLeftScreen] = useRecoilState(LeftScreenState);
  const [rightScreen, setRightScreen] = useRecoilState(RightScreenState);
  const [clicked, setClicked] = React.useState(false);
  const [triggerTask, setTriggerTask] = React.useState(false);
  const [exapndMenu, setExpandMenu] = useState(false);
  const OpenCloseMenu = () => {
    setExpandMenu(!exapndMenu);
  };

  const qs = require("query-string");
  const queryString = qs.parse(window.location.search);
  let history = useHistory();

  if (queryString.path) {
    history.push(queryString.path);
  }
  if (clicked == false) {
    ScreenControl(Constants.videoType.BAUCCTV);
    setRightScreen(Constants.videoType.BAUBrandwatch);
  }
  function onClickedMap() {
    setClicked(true);

    ScreenControl(Constants.videoType.floodCCTV);

    //setLeftScreen("Flood CCTV");
    setRightScreen(Constants.videoType.floodBrandWatch);
  }

  const sendCall = (value) => {
    setTriggerTask(true);
    setRightScreen(Constants.videoType.rapidDeploy);
  };

  return (
    <div className={"main-screen"}>
      <Header clicked={clicked} />

      <Row>
        <Col className={"left-container"}>
          {clicked === true ? (
            <IncomingMapData sendCall={sendCall} />
          ) : (
            <div class="bau-map">
              {" "}
              <div class="clickableMap" onClick={() => onClickedMap()}>
                {/* <span className={"text label"}>CLICK HERE</span> */}
              </div>
            </div>
          )}

          <NEA_WeatherTracking clicked={clicked} />
        </Col>

        <Col className={"right-container"}>
          <div class={"right-dashboard-home"}>
            <div className={"right-dashboard-head"}>Incident Priority List</div>
            <div class={"right-dashboard-container"}>
              {clicked === true ? (
                <IncomingIncidentData triggerTask={triggerTask} />
              ) : (
                <InitialIncidentData />
              )}
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
}

export default Home;
