import React from "react";
import { Row, Col } from "react-bootstrap";

const SubmitedLocation = (props) => {
  return (
    <div className={"location-info margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Location</div>
        </Col>
        <Col lg={9}>
          <div className={"row text-box-location"}>
            <div className={"locale"} />
            <span className={"span-text"}>{props.location}</span>
          </div>
          <div className={"row text-box-submit"}>
            <span className={"span-text"}>{props.street}</span>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SubmitedLocation;
