import React, { useState } from "react";

import Spin from "./spinningAnimation";

const LoadingSubmit = (props) => {
  return (
    <div>
      <h1 className="content-title">Situational Overview</h1>
      <div className={"header-info"}>
        <div className={"row justify-content-center"}>
          <div className={"margin-top-50"}>
            <div className="padding-bottom-20 centered">
              <div className="human-img" />
              <Spin />
            </div>
            <div className={"progress-text"}>
              Sending EMS request to KTP hospital
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default LoadingSubmit;
