import React from "react";
import { Row, Col, Button } from "react-bootstrap";

const SelectedCase = (props) => {
  return (
    <div className={"select-case margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Select case type:</div>
        </Col>
        <Col lg={9}>
          <div className={"row incident-button"}>
            <div
              value={props.buttonSelected}
              className="selected-button"
              //name="incidentbutton"
              //onClick={props.handleChange}
              // className={
              //   props.buttonChange.incidentbutton === "Medical Emergency"
              //     ? "btn-incident-active"
              //     : "btn-incident"
              // }
            >
              {props.buttonSelected}
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SelectedCase;
