import Card from "./card";
import React from "react";

export function WeatherDataCard() {
  return (
    <Card>
      <div className="weather-data padding-bottom-10">
        <h1 className="content-title">Weather Data</h1>
        <div className="content-log padding-top-30">
          <ul className="weather-data-1 text-left">
            <li>Forecast</li>
            <li>Temperature</li>
            <li>UV Index</li>
          </ul>
          <ul className="weather-data-2 text-right">
            <li>CLOUDY</li>
            <li>30C</li>
            <li>2 - LOW</li>
          </ul>

          <ul className="weather-data-3 text-left">
            <li>Rain Intensity</li>
            <li>Precipitation</li>
            <li>Wind</li>
          </ul>
          <ul className="weather-data-4 text-right">
            <li className="yellow-text">LOW</li>
            <li>10%</li>
            <li>2-3 KM/H</li>
          </ul>
        </div>
      </div>
      <div style={{ height: "20px" }} />
    </Card>
  );
}
