import React from "react";
import { Row, Col } from "react-bootstrap";

const Location = (props) => {
  return (
    <div className={"location-info margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Location</div>
        </Col>
        <Col lg={9}>
          <div className={"row text-box-lg"}>
            <div className={"locale"} />
            <span className={"span-text"}>{props.location}</span>
          </div>
          {/* <div className={"row text-box-lg"}>
            <span>{props.level}</span>
          </div> */}
          <input
            className={"row text-box-lg input-padding"}
            type="text"
            name="street"
            placeholder="Ask for additional location info"
            value={props.street}
            onChange={props.handleChange}
          />
        </Col>
      </Row>
    </div>
  );
};
export default Location;
