import React, { useState } from "react";
import { Row, Col, Button } from "react-bootstrap";

const SelectRate = (props) => {
  let bgButton;
  if (props.buttonSelected === "Life-threatening") {
    bgButton = "selected-button btn-danger";
  } else if (props.buttonSelected === "Emergency") {
    bgButton = "selected-button btn-warning";
  } else if (props.buttonSelected === "Minor emergency") {
    bgButton = "selected-button btn-info";
  } else {
    bgButton = "selected-button btn-success";
  }
  return (
    <div className={"select-rate margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Select severity rating:</div>
        </Col>
        <Col lg={9}>
          <div className={"row incident-button"}>
            <div value={props.buttonSelected} className={bgButton}>
              {props.buttonSelected}
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SelectRate;
