import React from "react";
import Card from "../Components/card";

export default function CitizenResponders() {
  return (
    <Card>
      <div className="myresponders">
        <h1 className="content-title">Community Responders</h1>
        <div className="content-log">
          <div>
            <span className="myresponders-second-span">CPR-AED Trained</span>
            <span className="span-after margin-left-10">5</span>
            <span className="myresponders-last-span">5 min away</span>
          </div>
          <div>
            <span className="myresponders-second-span">First Aid Trained</span>
            <span className="span-after margin-left-10">3</span>
            <span className="myresponders-last-span">5 min away</span>
          </div>
        </div>
      </div>
    </Card>
  );
}
