import React from "react";
import { Row, Col } from "react-bootstrap";

const SummaryLocation = (props) => {
  return (
    <div className={"location-info margin-top-20"}>
      <Row>
        <Col lg={4} className={"padding-left-40"}>
          <div className={"text"}>Location</div>
        </Col>
        <Col lg={8}>
          <div className={"row"}>
            {/* <div className={"locale"} /> */}
            <span className={"span-text"}>{props.location}</span>
          </div>
          {/* <div className={"row text-box-lg"}>
            <span>{props.street}</span>
          </div> */}
        </Col>
      </Row>
    </div>
  );
};
export default SummaryLocation;
