import React from "react";
import Card from "../Components/card";
import HospitalTab from "../Components/hospital-tab";
import AssetTab from "../Components/asset-tab";
import Constants from "../Constants";
import { Row, Col } from "react-bootstrap";
const ListOfAsset = () => {
  return (
    <div className="scrollbar">
      {Constants.AssetUnit.map((unit) => (
        <AssetTab data={unit} />
      ))}
    </div>
  );
};

const ListOfHospital = () => {
  return (
    <div className="scrollbar">
      <HospitalTab
        hostpitalName="Raffles Valley Hospital"
        hospitalDistance="4 min away from RCT"
        capacity="83"
        personel={297}
        doctor={20}
        patient={350}
        bed={30}
        tags={["BURN CENTRE", "CARDIAC CENTRE", "TRAUMA CENTRE"]}
      />

      <HospitalTab
        hostpitalName="Emerald Hospital"
        hospitalDistance="7 min away from RCT"
        capacity="65"
        personel={1280}
        doctor={65}
        patient={1700}
        bed={300}
        tags={[
          "BURN CENTRE",
          "AMBULATORY SURGICAL CENTRE",
          "ORTHOPAEDIC CENTRE",
        ]}
      />
      <HospitalTab
        hostpitalName="Killiney Hospital"
        hospitalDistance="13 min away from RCT"
        capacity="54"
        personel={766}
        doctor={365}
        patient={11354}
        bed={265}
        tags={["TRAUMA CENTRE", "RESPIRATORY MEDICINE"]}
      />
    </div>
  );
};

export default function Asset_Hospital(toggleState, toggleTab) {
  return (
    <Card>
      <Row className="bloc-tabs">
        <Col
          lg={6}
          onClick={() => toggleTab(1)}
          className={toggleState === 1 ? "tabs active-tabs" : "tabs"}
        >
          <div className="tab-content-title">Asset Management</div>
        </Col>

        <Col
          lg={6}
          onClick={() => toggleTab(2)}
          className={toggleState === 2 ? "tabs active-tabs" : "tabs"}
        >
          <div className="tab-content-title">Hospital Capacity</div>
        </Col>
      </Row>
      <div className="asset-management">
        {/* <h1 className="content-title">Asset Management</h1> */}
        <div className="content-tabs">
          {toggleState === 1 && <ListOfAsset />}
          {toggleState === 2 && <ListOfHospital />}
        </div>
      </div>
    </Card>
  );
}
