import React from "react";
import { RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";
import Constants from "../Constants";
import RapidDeployCCTV from "./RapidDeployCCTV";
import Iframe from "react-iframe";

function RightScreen() {
  const [rightScreenState, setRightScreenState] =
    useRecoilState(RightScreenState);
  const location = useLocation();
  return (
    <div className={"right-screen-container"}>
      <div className={""}>
        {location.pathname !== "/citizen-watch" && (
          <div>
            {rightScreenState == Constants.videoType.BAUBrandwatch && (
              <div className={"brand-watch-bau"} />
            )}
            {rightScreenState == Constants.videoType.floodBrandWatch && (
              <div className={"brand-watch-flood"} />
            )}
            {rightScreenState == Constants.videoType.rapidDeploy && (
              <RapidDeployCCTV />
            )}
            {rightScreenState == Constants.videoType.c4Incident && (
              <Iframe
                src="https://www.google.com/maps/embed?pb=!4v1632193234862!6m8!1m7!1s4QsJMmApr1KEc7GsSQiM3Q!2m2!1d1.29509646565762!2d103.8531412532597!3f184.44!4f18.760000000000005!5f0.7820865974627469"
                width="1920"
                height="1080"
                style="border:0;"
                allowfullscreen=""
                loading="lazy"
              ></Iframe>
            )}
          </div>
        )}
        {location.pathname == "/citizen-watch" && (
          <Iframe
            src="https://www.google.com/maps/embed?pb=!4v1631113214572!6m8!1m7!1sA0ISCAk98FqPtsmpxKMfDg!2m2!1d1.413833490241437!2d103.8343286338291!3f331.9039209430127!4f-2.8433831045208393!5f0.7820865974627469"
            width="1920"
            height="1080"
            style="border:0;"
            allowfullscreen=""
            loading="lazy"
          ></Iframe>
        )}
      </div>
    </div>
  );
}

export default RightScreen;
