import React from "react";
import { Row } from "react-bootstrap";
import { NavLink } from "react-router-dom";

function IncidentHeader() {
  return (
    <div className="header-incident">
      <Row>
        <div className="header-incident-content container-dashboard">
          <div className="incident-top-left float-left">
            <div className="incident-left-top">
              <div className="incident-top-address">RAFFLES CITY TOWER</div>
              <div className={"margin-bottom-10"} />
              <div className="incident-second-address">
                250 North Bridge Rd, Singapore 179101
              </div>
              <div className="incident-third-address">
                44 OFFICES, 3250 OCCUPANTS
              </div>
            </div>
            <div className="incident-right-top">
              <div className="max-height-30">
                <p className="incident-type">Type of Incident</p>
                <div className="type">Commercial Building Fire</div>
              </div>
              {/* <div style={{ maxHeight: 30 }}> */}
              <div className="max-height-30">
                <p className="estimate-injury">Number of Casualties</p>
                <p className="injury-num">
                  25 - <span className="injury-num-red">HIGH</span>
                </p>
              </div>
              <div className="max-height-30">
                <div className="key-personel">Key Personnel</div>
                <div className="key-personel-desc">Tan Hock Yew, SMC</div>
              </div>
            </div>
          </div>

          <div className="float-right">
            <div class="container-button">
              <ul class="progressbar">
                <li>
                  <NavLink exact activeClassName="active" to="/incident-report">
                    <p>1</p>
                    <span>Overview</span>
                  </NavLink>
                </li>

                <li>
                  <NavLink activeClassName="active" to="/incident-threats">
                    <p>2</p>
                    <span>Predicted Threats</span>
                  </NavLink>
                </li>

                <li>
                  <NavLink activeClassName="active" to="/incident-response">
                    <p>3</p>
                    <span>SOP</span>
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </Row>
    </div>
  );
}

export default IncidentHeader;
