import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  DropdownButton,
  Dropdown,
  Button,
  InputGroup,
  FormControl,
  Table,
  thead,
  tr,
  th,
} from "react-bootstrap";

import WidgetValue from "../Components/widget-value";
import Constants from "../Constants";

const AssetTab = (props) => {
  const data = props.data;

  const isToogle = data.isToogle;

  const [toogle, setToogle] = useState({
    visible: false,
  });

  const OpenToogle = () => {
    const noToogle = isToogle;
    if (noToogle === false) {
      setToogle({
        visible: false,
      });
    } else {
      setToogle({
        visible: true,
      });
    }
  };

  const AssetCard = (props) => {
    if (toogle.visible == false) {
      return (
        <div className="asset-container">
          <div
            className="asset-list-item"
            onClick={() => {
              OpenToogle();
            }}
          >
            <AssetTopCard data={data} />
          </div>
        </div>
      );
    } else {
      return (
        <div className="asset-container">
          <div
            className="asset-list-item"
            onClick={() => {
              setToogle({ visible: false });
            }}
          >
            <AssetTopCard data={data} />
          </div>

          <AssetContentCard data={data} />
        </div>
      );
    }
  };

  const CaretArrow = (props) => {
    if (toogle.visible == false) {
      return (
        <div className="float-right">
          <a class="caret collapse"></a>
        </div>
      );
    } else {
      return (
        <div className="float-right">
          <a class="caret collapsed"></a>
        </div>
      );
    }
  };

  const AssetTopCard = (props) => {
    console.log(isToogle);
    return (
      <div>
        <Row>
          <Col lg={7}>
            <div className={"h-name"}>{data.assetName}</div>
          </Col>
          <Col lg={5}>
            <div className="inline-row-right">
              <div className={"h-distance"}>{data.assetDistance}</div>
            </div>
          </Col>
        </Row>
        <Row className="margin-top-10">
          <Col lg={2}>
            <div className="person-logo"></div>
            <div className={"span-after margin-left-5"}>{data.inchargeNum}</div>
          </Col>
          <Col lg={5} className={"padding-0"}>
            <div className="inline-row-left padding-0">
              <div className="officer-logo"></div>
              <div className={"span-after-logo"}>{data.incharge}</div>
            </div>
          </Col>
          <Col lg={5} className={"padding-0"}>
            <div className="inline-row-left">
              <div className={"span-asset-incharge"}>{data.inchargePerson}</div>
              {isToogle == true && <CaretArrow />}
            </div>
          </Col>
        </Row>
      </div>
    );
  };

  const AssetResponse = (props) => {
    const response = props.data;

    return (
      <div className="list-item">
        <Row>
          <Col lg={10}>
            <span>{response.Response}</span>
          </Col>
          <Col lg={2}>
            <span className="value">{response.ResponseNum}</span>
          </Col>
        </Row>
      </div>
    );
  };

  const AssetContentCard = (props) => {
    const unit = data.AssetUnits;
    console.log(unit);
    return (
      <div className="asset-list-content">
        <div className="list-box">
          {data.AssetUnits.map((assetUnit) => (
            <div className="box-value">
              <WidgetValue
                value={assetUnit.UnitNum}
                prefix={""}
                title={assetUnit.Unit}
                startvalue={3.12}
                duration={3}
                decimals={0}
              />
            </div>
          ))}
        </div>

        <div className="list-container margin-top-20">
          {data.AssetResponse.map((responseUnit) => (
            <AssetResponse data={responseUnit} />
          ))}
        </div>
      </div>
    );
  };

  return <AssetCard />;
};

export default AssetTab;
