import React from "react";
import Card from "../Components/card";

export default function Evacuation() {
  return (
    <Card>
      <div className="evacuation-analysis padding-bottom-95">
        <h1 className="content-title">Evacuation Analysis</h1>
        <div className="content-log">
          <div>
            <p className="evacuation-analysis-list-title">Evacuation Source</p>
          </div>
          <div>
            <span className="evacuation-analysis-list">
              {" "}
              Raffles City Tower
            </span>
          </div>
          <div className="padding-top-20" />
          <div>
            <p className="evacuation-analysis-list-title">Evacuation Point</p>
          </div>
          <div>
            <span className="evacuation-analysis-list">
              {" "}
              The Padang (550M Away)
            </span>
          </div>
          <div className="padding-top-20" />
          <div>
            <p className="evacuation-analysis-list-title">Evacuation Size</p>
          </div>
          <div>
            <span className="evacuation-analysis-list"> 3250 People</span>
          </div>

          <div className="padding-top-20">
            <div className="evacuation-analysis-list-title">
              Potential Impediments
            </div>
          </div>
          <div>
            <div className="evacuation-analysis-list">
              - Vehicle congestion on Bras Basah Road
            </div>
            <div>
              <div className="evacuation-analysis-list">
                - Minor flooding on pavements
              </div>
            </div>
          </div>

          <div className="padding-top-20">
            <p className="evacuation-analysis-list-title">
              Evacuation Time Based On Simulation
            </p>
          </div>
          <div>
            <span className="evacuation-analysis-list">30 Minutes</span>
          </div>
        </div>
      </div>
    </Card>
  );
}
