import React from "react";
import { Row, Col } from "react-bootstrap";

const SummaryIncident = (props) => {
  return (
    <div className={"select-incident margin-top-20"}>
      <Row>
        <Col lg={4} className={"padding-left-40"}>
          <div className={"text"}>Incident</div>
        </Col>
        <Col lg={8}>
          <div className={"row incident-button"}>
            <div value={props.buttonSelected} className="selected-button">
              {props.buttonSelected}
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default SummaryIncident;
