import React, { useState } from "react";
import { Row, Col } from "react-bootstrap";
import { Link, useHistory, useLocation } from "react-router-dom";
import Icons from "../Components/icons";
import Clock from "react-live-clock";

function DeploymentBar(props) {
  const history = useHistory();
  const location = useLocation();

  const moment = require("moment");

  const [currentTime, setCurrentTime] = useState(
    moment().format("MMMM DD YYYY, HH:MM A")
  );

  const onLaunch = () => {
    if (props.launchroute != "rapiddeploy") {
      history.push(props.launchroute);
    }
  };

  const TimerCount = () => {
    setTimeout(() => {
      const currentTime = moment().format("MMMM DD YYYY, HH:MM A");
      TimerCount();
    }, 55000);
  };

  TimerCount();

  return (
    <div className="modifiedCard ">
      <div className="container-dashboard">
        <Row>
          <Col
            // style={{ maxWidth: "480px" }}
            lg={3}
          >
            <div className="inline-row padding-5">
              <Link to="/">
                <button className="margin-right-10 padding-horizontal-10 padding-bottom-5">
                  <Icons.Home />
                </button>
              </Link>

              <button
                onClick={() => {
                  history.goBack();
                }}
                className="margin-right-10 padding-horizontal-10 padding-bottom-5"
              >
                <Icons.Back />
              </button>

              <button
                onClick={onLaunch}
                className={
                  location.pathname === "/incident-threats"
                    ? "button-teko button-disabled padding-left-80 padding-right-80 max-width"
                    : "button-teko padding-left-80 padding-right-80 max-width"
                }
              >
                {props.launchname}
              </button>
            </div>
          </Col>
          <Col lg={6}>
            <div className="current-time">
              {/* {currentTime} */}
              <Clock
                className="current-time padding-left-15"
                format={"ddd  14:47  MMM DD,YYYY"}
                ticking={true}
                timezone={"Asia/Singapore"}
                filter={(date) => date.toUpperCase()}
              />
            </div>
          </Col>
          <Col lg={3}>
            <div className="inline-row-right padding-5">
              <button
                onClick={onLaunch}
                className={
                  location.pathname === "/incident-threats"
                    ? "button-teko button-disabled padding-left-80 padding-right-80 max-width"
                    : "button-teko padding-left-80 padding-right-80 max-width"
                }
              >
                {props.launchname}
              </button>

              <button
                onClick={() => {
                  history.goBack();
                }}
                className="margin-right-10 padding-horizontal-10 padding-bottom-5"
              >
                <Icons.Back />
              </button>

              <Link to="/">
                <button className="margin-right-10 padding-horizontal-10 padding-bottom-5">
                  <Icons.Home />
                </button>
              </Link>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );

  return <div className="modifiedCard container-dashboard"></div>;
}

export default DeploymentBar;
