import React, { useState, useEffect } from "react";

import { Row, Col, Modal, Button } from "react-bootstrap";
import Card from "../Components/card";

import CRPSteps from "./crpSteps";

import Form from "./form-submit";
import Report from "./summary-report";
import ActivitySummary from "./cs-activitylog/cw-activitysummary";

import ScreenControl from "../screencontrol";
import Constants from "../Constants";

const CRPForm = (props) => {
  const data = props.data;
  const edit = props.editButton;
  const endCall = props.endCall;
  const [show, setShow] = useState(false);
  const [modal, setModal] = useState(false);
  const [Summary, setSummary] = useState(false);

  const [loading, setLoading] = useState(false);
  // trigger the second modal
  const handleNewModal = () => {
    setModal(true);
    console.log("modal");
  };
  // triggre the submit report
  const handleFinalModal = () => {
    console.log("open");
    setSummary(true);
    setTimeout(() => {
      setLoading(true);
      console.log("close");
    }, 1500);
    console.log("send report");
    ScreenControl(Constants.videoType.surveyVideo);
  };

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  const [clicked, setClicked] = useState({
    isToogleOn: false,
  });

  const handleClick = () => {
    setClicked((prev) => ({
      isToogleOn: !prev.isToogleOn,
    }));
  };

  const [num, setNum] = useState(1);
  const incNum = () => {
    setNum(num + 1);
  };
  const decNum = () => {
    if (num > 1) {
      setNum(num - 1);
    } else {
      setNum(1);
    }
  };

  const onHandleClear = () => {
    handleClose();
    window.location.reload();
  };

  const onEndCall = (event) => {
    endCall();
    handleShow();
  };

  return (
    <div className={"header-info scrollbar"}>
      {/* CRP Steps List */}
      <CRPSteps />

      <Row className={"justify-content-center"}>
        <div className={"margin-top-50 margin-bottom-50"}>
          <Button
            type="submit"
            onClick={onEndCall}
            className={"request-submitted"}
          >
            EMS handoff
          </Button>
        </div>
      </Row>

      <Row className={"justify-content-center"}>
        {modal == false ? (
          <Modal show={show} onHide={handleClose}>
            {/* First Modal */}

            <Card>
              <div className="citizen-overview">
                <h1 className="content-title">Summary Report</h1>
                <div className={"header-info"}>
                  {/* First Modal Content */}
                  <Button
                    type="button"
                    onClick={edit}
                    className={"edit-button"}
                  >
                    <span className={"edit-pen"}></span>
                    Edit Details
                  </Button>

                  <Form.SubmitedCaller CallerId={data.callerId} />

                  <Form.SubmitedLocation
                    location={data.location}
                    street={data.street}
                  />

                  <Form.SubmitedIncidentDetails
                    incidentDetails={data.incidentDetails}
                  />

                  <Form.SelectedIncident buttonSelected={data.incidentbutton} />

                  <Form.SelectedCase buttonSelected={data.casebutton} />

                  <Form.SelectedRate buttonSelected={data.ratebutton} />

                  <div className={"margin-top-20 margin-bottom-20"}>
                    <Row className={"justify-content-center"}>
                      <div className={"text border-line"}>
                        Additional Details
                      </div>
                    </Row>
                  </div>

                  <Form.SubmittedVideo />
                  {/* <SubmittedCount /> */}
                  <div className={"device-use margin-top-10"}>
                    <Row>
                      <Col lg={3} className={"padding-left-40"}>
                        <div className={"text"}>Device used:</div>
                      </Col>
                      <Col lg={9}>
                        <div className={"row"}>
                          <label className="switch">
                            <input type="checkbox" onClick={handleClick} />
                            <div className="slider round"></div>
                          </label>
                          <span className={"text margin-left-10 margin-top-5"}>
                            AED
                          </span>
                        </div>
                        {clicked.isToogleOn ? (
                          <div className={"row"}>
                            <div className={"text-counter"}>
                              Number of shocks
                            </div>
                            <Button
                              type="button"
                              className={"minus"}
                              onClick={decNum}
                            >
                              <div className={"minus-icon"}></div>
                            </Button>
                            <div className={"count-num"}>
                              <div className={"num"}>{num}</div>
                            </div>
                            <Button
                              type="button"
                              className={"plus"}
                              onClick={incNum}
                            >
                              <div className={"plus-icon"}></div>
                            </Button>
                          </div>
                        ) : (
                          <></>
                        )}
                      </Col>
                    </Row>
                  </div>

                  <Row className={"justify-content-center"}>
                    <div className={"margin-top-30"}>
                      <Button
                        type="submit"
                        onClick={handleNewModal}
                        className={
                          clicked.isToogleOn
                            ? "request-submitted"
                            : "button-disabled request-submitted"
                        }
                      >
                        Continue
                      </Button>
                    </div>
                  </Row>
                </div>
              </div>
            </Card>
          </Modal>
        ) : (
          <Modal show={show} onHide={handleClose}>
            {/* Second Modal */}
            <Card>
              <div className="citizen-overview">
                <div
                  className={"justify-content-between content-border d-flex"}
                >
                  <h1 className="summary-title">Summary Report</h1>
                  {Summary == false && (
                    <Button
                      type="submit"
                      onClick={handleFinalModal}
                      className={
                        "request-submitted margin-top-15 margin-right-15"
                      }
                    >
                      Submit summary report
                    </Button>
                  )}
                  {loading == true && (
                    <Button
                      type="submit"
                      onClick={onHandleClear}
                      className={
                        "request-submitted margin-top-15 margin-right-15"
                      }
                    >
                      Close this case
                    </Button>
                  )}
                </div>
                {Summary == false ? (
                  <div className={"header-info scrollbar"}>
                    <Report.SummaryWidget />
                    {/* Second Modal Content */}
                    <Row>
                      <Col lg={8}>
                        {/* Left Content */}
                        <Report.SummaryRate buttonSelected={data.ratebutton} />

                        <Report.SummaryIncident
                          buttonSelected={data.incidentbutton}
                        />

                        <Report.SummaryCase buttonSelected={data.casebutton} />

                        <Report.SummaryCaller CallerId={data.callerId} />

                        <Report.SummaryLocation location={data.location} />

                        <Report.SummaryIncidentDetails
                          incidentDetails={data.incidentDetails}
                        />

                        <Report.SummaryResponder responder="William Ng (8123 1234)" />

                        <Report.SummaryDeviceUsed deviceUsed={num} />

                        <Report.SummaryReceipt />
                      </Col>
                      <Col lg={4}>
                        <div className={"border-left"}>
                          {/* Right Content */}
                          <Report.SummaryDateInfo dateInfo="23 August 2021" />

                          <Report.SummaryTimeInfo timeInfo="Time of Incident: 14:39" />

                          <Report.SummaryArrivedInfo arrivedInfo="EMS Arrival: 14:48" />

                          <Report.SummaryHealthRecord />
                          <Report.SummaryVideo />
                        </div>
                      </Col>
                    </Row>

                    {/* Event Sequence Here */}
                    <Row className={""}>
                      <div className={"event-sequence"}>
                        <div className={"title text-center padding-bottom-20"}>
                          Call Log
                        </div>

                        <ActivitySummary />

                        <div
                          className={
                            "bottom-text text-center padding-top-20 padding-bottom-20"
                          }
                        >
                          <span className={"borderline-left"}></span>
                          Nothing follows
                          <span className={"borderline-right"}></span>
                        </div>
                      </div>
                    </Row>

                    {/* dispatcher here */}
                    <Row className={"justify-content-center"}>
                      <div className={"margin-top-40"}>
                        <div className={"text text-center"}>
                          Prepared by dispatcher: SGT Janelle Tan
                        </div>
                        <div className={"text text-center"}>
                          23/7/2021, 14:48
                        </div>
                      </div>
                    </Row>

                    <Row className={"justify-content-center"}>
                      <Col lg={4}>
                        <div className={"margin-top-50 margin-bottom-50"}>
                          <Button
                            type="submit"
                            onClick={() => setModal(false)}
                            className={"back-submitted"}
                          >
                            Back
                          </Button>
                        </div>
                      </Col>
                      <Col lg={4}>
                        <div className={"margin-top-50"}>
                          <Button
                            type="submit"
                            onClick={handleFinalModal}
                            className={"request-submitted"}
                          >
                            Submit summary report
                          </Button>
                        </div>
                      </Col>
                    </Row>
                  </div>
                ) : (
                  <div>
                    {loading == false ? (
                      <Report.LoadingSummary />
                    ) : (
                      <Report.SummaryReport />
                    )}
                  </div>
                )}
              </div>
            </Card>
          </Modal>
        )}
      </Row>
    </div>
  );
};
export default CRPForm;
