import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import IncidentHeader from "../Components/IncidentHeader";
import SituationOverview from "../Components/SitutationOverviewCard";
import DeploymentBar from "../Components/deployment";
import CCTVCard from "../Components/CCTV";
import ActivityLog from "../Components/ActivityLogCard";
import DroneCapture from "../Components/DroneCaptureCard";
import LocationData from "../Components/LocationData";
import { RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";

import ScreenControl from "../screencontrol";
import Constants from "../Constants";

function IncidentOverview() {
  const [progress, setProgress] = useState(0);
  if (progress < 200) {
    setTimeout(() => {
      setProgress(progress + 3);
    }, 30);
  }

  const location = useLocation();
  const [rightScreen, setRightScreen] = useRecoilState(RightScreenState);
  if (location.pathname === "/incident-report") {
    ScreenControl(Constants.videoType.c4News);
    setRightScreen(Constants.videoType.c4Incident);
  }
  return (
    <div>
      <IncidentHeader />
      <DeploymentBar launchname="NEXT" launchroute="/incident-threats" />
      <div className="padding-top-20" />
      <div className={"container-dashboard"}>
        <Row>
          <Col
            className="col-3 padding-bottom-20"
            //   style={{ maxWidth: "480px" }}
          >
            <ActivityLog repeat={true} />
            {/* each card container space */}
            <div className="padding-top-20"></div>
            <LocationData />
          </Col>
          <Col
            className="col-6"
            //   style={{ maxWidth: "880px" }}
          >
            <div className="incident-map"></div>
            <div className="padding-top-20" />
            <SituationOverview />
          </Col>
          <Col
            className="col-3"
            //   style={{ maxWidth: "480px" }}
          >
            {CCTVCard(progress)}
            {/* each card container space */}
            <div className="padding-top-20"></div>
            <DroneCapture />
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default IncidentOverview;
