import React, { useState, useEffect, useRef } from "react";
import Card from "../Components/card";
import Type from "./typing";

const activities = [
  {
    time: "[ 14 : 35 : 03 ]",
    content: "Fire alarm and smoke detectors activated.",
  },
  {
    time: "[ 14 : 38 : 21 ]",
    content: "Fire reported on RCT Level 35, evacuation initiated by SMC.",
  },
  {
    time: "[ 14 : 40 : 48 ]",
    content: "2 firetrucks from Central Fire Station deployed.",
  },
  { time: "[ 14 : 42 : 13 ]", content: "Central police station notified." },
  { time: "[ 14 : 45 : 38 ]", content: "CCTV access authorized by SMC." },
  { time: "[ 14 : 45 : 48 ]", content: "Observation drone deployed." },
  { time: "[ 14 : 46 : 15 ]", content: "Firetrucks arrived on scene." },
];

export default function ActivityLog(props) {
  return (
    <Card>
      <div className="activity-log">
        <h1 className="content-title">Activity Log</h1>
        <div id="ListOfActivities" className="activity-content-log">
          {props.repeat === true ? (
            <Type text={activities} />
          ) : (
            <div>
              {activities.map((val) => (
                <p className="activity-log-time">
                  {val.time}
                  <br />
                  <span className="activity-log-desc">
                    {val.content}
                    <br />
                  </span>
                  <br />
                </p>
              ))}
            </div>
          )}
        </div>
      </div>
    </Card>
  );
}
