import React from "react";
import { Col, Row } from "react-bootstrap";
import Card from "../Components/card";
import Video from "./video";
import Drone from "../Assets/drone-footage.mp4";

export default function DroneCapture() {
  return (
    <Card>
      <div className="drone-capture padding-bottom-25">
        <h1 className="content-title">Drone Footage</h1>
        <div className="content-log">
          <div>
            <div className="drone-capture-content">
              <Row>
                {" "}
                <div className={"stats-row"}>
                  <div className={"d-flex"}>
                    <div className={"signal-icon"}></div>
                    <div className={"drone-capture-text"}>GOOD</div>
                  </div>
                  <div className={"d-flex"}>
                    <div className={"battery-icon"}></div>
                    <span className={"drone-capture-text"}>86%</span>
                  </div>
                </div>
                {/* <div className={"stats-row"}>
                  <div className={"speed-icon"}></div>
                  <span className={"drone-capture-text"}>27 KM/H</span>
                </div> */}
                {/* <div className={"stats-row"}>
                  <div className={"battery-icon"}></div>
                  <span className={"drone-capture-text"}>86%</span>
                </div> */}
                <div>
                  <span className={"drone-capture-value"}>1h 12m left</span>
                </div>
              </Row>
              <Row>
                <div>
                  <span className={"drone-capture-text"}>Altitude</span>
                  <span className={"drone-capture-value"}>21.336 M</span>
                </div>
                <div>
                  <span className={"drone-capture-text"}>Long</span>
                  <span className={"drone-capture-value"}>103.8528 N</span>
                </div>
              </Row>
              <Row>
                <div>
                  {" "}
                  <span className={"drone-capture-text"}>Current Location</span>
                  <span className={"drone-capture-value"}>
                    Raffles City Tower
                  </span>
                </div>
                <div>
                  {" "}
                  <span className={"drone-capture-text margin-left-15"}>
                    Lat
                  </span>
                  <span className={"drone-capture-value"}>1.2938 E</span>
                </div>
              </Row>
            </div>

            <div className="video-container">
              <Video
                //vid={"https://youtu.be/R0rNsLULlPI"}
                vid={Drone}
                width={405}
                height={228}
                className="video-container.fullscreen"
                loop={true}
                muted={true}
              />
            </div>
          </div>
        </div>
      </div>
    </Card>
  );
}
