import React from "react";
import { Row, Col } from "react-bootstrap";

const IncidentDetails = (props) => {
  const onClick = () => {
    props.setText();
  };
  return (
    <div className={"incident-info margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Incident details</div>
        </Col>
        <Col lg={9}>
          {/* <div className={"row text-box-lg"}>
            <span>{props.incidentDetails}</span> 
          </div> */}
          <input
            className={"row text-box-lg input-padding"}
            type="text"
            name="incidentDetails"
            placeholder="Ask for details on what is happening"
            value={props.incidentDetails}
            onChange={props.handleChange}
            onClick={onClick}
          />
        </Col>
      </Row>
    </div>
  );
};
export default IncidentDetails;
