import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  DropdownButton,
  Dropdown,
  Button,
  InputGroup,
  FormControl,
  Table,
  thead,
  tr,
  th,
} from "react-bootstrap";

import WidgetValue from "../Components/widget-value";
import ProgressBar from "./progress-bar";
import Constants from "../Constants";

const HospitalTab = (props) => {
  const hostpitalName = props.hostpitalName;
  const hospitalDistance = props.hospitalDistance;
  const capacity = props.capacity;

  const personel = props.personel;
  const doctor = props.doctor;
  const patient = props.patient;
  const bed = props.bed;

  const tags = props.tags;

  return (
    <div className="hospital-container">
      <Row>
        <Col lg={7}>
          <div className={"h-name"}>{hostpitalName}</div>
        </Col>
        <Col lg={5}>
          <div className="inline-row-right">
            <div className={"h-distance"}>{hospitalDistance}</div>
          </div>
        </Col>
      </Row>

      <Row>
        <Col>
          <div className="inline-row  margin-top-10">
            <div className={"capacity"}>Operating Capacity</div>
            <ProgressBar percent={capacity} />
          </div>
        </Col>
      </Row>

      {/* hopital stats*/}

      <div className="list-box margin-top-10">
        <div className="box-value">
          <WidgetValue
            value={personel}
            prefix={""}
            fontsize={"medium"}
            title={"Active Personnel"}
            startvalue={3.12}
            duration={3}
            decimals={0}
          />
        </div>

        <div className="box-value">
          <WidgetValue
            value={doctor}
            prefix={""}
            fontsize={"medium"}
            title={"ICU Doctors"}
            startvalue={3.12}
            duration={5}
            decimals={0}
          />
        </div>

        <div className="box-value">
          <WidgetValue
            value={patient}
            prefix={""}
            fontsize={"medium"}
            title={"Patients"}
            startvalue={3.12}
            duration={3}
            decimals={0}
          />
        </div>

        <div className="box-value">
          <WidgetValue
            value={bed}
            prefix={""}
            fontsize={"medium"}
            title={"Bed Capacity"}
            startvalue={15.12}
            duration={2}
            decimals={0}
          />
        </div>
      </div>

      <div className="inline-row  margin-top-15">
        <div className={"capability"}>Relevant Capabilities</div>
      </div>

      <Row className={"margin-top-10 margin-0"}>
        {/* hopital  Tags*/}
        {tags.map((item) => (
          <div className="tag">{item}</div>
        ))}
      </Row>
    </div>
  );
};

export default HospitalTab;
