import React, { useState, useEffect } from "react";
import Clock from "react-live-clock";

function Header(props) {
  const [title, setTitle] = useState(0);
  const [busIncident, setBusIncident] = useState(0);
  const [roadIncident, setRoadIncident] = useState(0);

  const bauTitle = (
    <div className={"h-title"}>No Major Incidents Around Singapore</div>
  );
  const nextTitle = (
    <div className={"h-title warning"}>
      Multiple Flash Floods across Western and Central Singapore
    </div>
  );

  const bauBusIncident = (
    <div className={"ml-text value"}>No major disruptions</div>
  );
  const nextBusIncident = (
    <div className={"ml-text warning"}>50, 52 diverted from Dunearn Road</div>
  );

  const bauRoadIncident = (
    <div className={"ml-text warning"}>Vehicle breakdown at AYE</div>
  );
  const nextRoadIncident = (
    <div className={"ml-text warning"}>
      Major traffic jam along PIE, Dunearn Road
    </div>
  );

  if (props.clicked) {
    if (title === 0) {
      setTitle(1);
    }
    if (busIncident === 0) {
      setBusIncident(1);
    }
    if (roadIncident === 0) {
      setRoadIncident(1);
    }
  }

  return (
    <div class="header-container">
      <div className={"header"}>
        {title === 0 && bauTitle}
        {title === 1 && nextTitle}
        <div className={"menu-liner"}>
          <div className={"liner-box"}>
            <div className={"train-icon"}></div>
            <div className={"padding-5"}>
              <div className={"ml-text label"}>Train Services </div>
              <div className={"ml-text value"}>No major disruptions</div>
            </div>
          </div>
          <div className={"liner-box"}>
            <div className={"bus-icon"}></div>
            <div className={"padding-5"}>
              <div className={"ml-text label"}>Bus Services </div>
              {busIncident === 0 && bauBusIncident}
              {busIncident === 1 && nextBusIncident}
            </div>
          </div>
          <div className={"liner-box"}>
            <div className={"road-icon"}></div>
            <div className={"padding-5"}>
              <div className={"ml-text label"}>Major Road Networks </div>
              {roadIncident === 0 && bauRoadIncident}
              {roadIncident === 1 && nextRoadIncident}
            </div>
          </div>
        </div>
      </div>

      <Clock
        className="headerClock"
        format={"ddd HH:mm MMM DD, YYYY "}
        ticking={true}
        timezone={"Asia/Singapore"}
      />
    </div>
  );
}

export default Header;
