import React, { useState } from "react";
import {
  Container,
  Row,
  Col,
  DropdownButton,
  Dropdown,
  Button,
  InputGroup,
  FormControl,
  Table,
  thead,
  tr,
  th,
  Modal,
  ButtonToolbar,
  ButtonGroup,
} from "react-bootstrap";
import Card from "../Components/card";
import Constants from "../Constants";
import Icons from "../Components/icons";

function SopForm(props) {
  const [sopForm, setSopForm] = useState({
    visible: false,
    data: [
      {
        item: "1-1",
      },
      {
        item: "1-1",
      },
    ],
  });

  const [remarkModalVisibility, setRemarkModalVisibility] = useState(false);

  const OpenSopForm = (procedure) => {
    console.log("procedure", procedure);

    setSopForm({
      visible: true,
      ...procedure,
    });
    console.log("OpenSopForm");
  };
  console.log(Constants.Sop);
  const [status1, setStatus1] = useState([]);
  const [status2, setStatus2] = useState([]);
  const [updateMap, setUpdateMap] = useState(false);
  props.onChangeMap(updateMap);

  const SopCard = (props) => {
    if (sopForm.visible == false) {
      return (
        <div className="container-scroll">
          {Constants.Sop.map((procedure) => (
            <Operate
              data={procedure}
              id={procedure.agency}
              onClick={() => {
                OpenSopForm(procedure);
              }}
            />
          ))}
        </div>
      );
    } else {
      return (
        <div className="container-scroll">
          {sopForm.procedures.map((procedure) => (
            <SopItem data={procedure} id={sopForm.agency} />
          ))}
        </div>
      );
    }

    return <div />;
  };

  function SopItem(props) {
    const proc = props.data;
    const id = props.id;

    console.log("props");
    const [sopState, setSopState] = useState("Not Started");

    if (id == "Building Security") {
      setStatus1("RCT");
    }
    if (id == "SPF/SCDF") {
      setStatus2("SPF");

      setUpdateMap(true);
    }

    const [addNoteVisibility, setAddNoteVisibility] = useState(false);
    return (
      <div className="hbar-list-item-form" {...props}>
        <Row className="hbar-row">
          <Col lg="5">
            <div className="margin-bottom-5"> {proc.action}</div>

            <div className="note-info">
              <span>
                <Icons.Person height={12} width={12} />{" "}
              </span>
              <span> {proc.assignee}</span>
            </div>
          </Col>

          <Col lg="4">
            <a href="#" className={"disabled"}>
              {proc.execute}
            </a>
          </Col>

          <Col lg="3">
            <div className="inline-row">
              <div className="margin-right-10">
                {!addNoteVisibility && (
                  <button onClick={() => setAddNoteVisibility(true)}>
                    <svg
                      enable-background="new 0 0 512 512"
                      height="22"
                      width="22"
                      viewBox="0 0 512 512"
                    >
                      <g fill="white">
                        <path d="m503.22 186.828-25.041-25.041c-11.697-11.698-30.729-11.696-42.427 0l-52.745 52.745v-169.532c0-24.813-20.187-45-45-45h-293c-24.813 0-45 20.187-45 45v422c0 24.813 20.187 45 45 45h293c24.813 0 45-20.187 45-45v-119.033l120.213-118.712c11.697-11.697 11.697-30.73 0-42.427zm-179.399 177.423-45.122 21.058 21.037-45.078 111.975-111.975 24.757 24.756zm29.186 102.749c0 8.271-6.729 15-15 15h-293c-8.271 0-15-6.729-15-15v-422c0-8.271 6.729-15 15-15h293c8.271 0 15 6.729 15 15v199.532c-83.179 83.179-77.747 77.203-79.34 80.616l-39.598 84.854c-2.667 5.717-1.474 12.49 2.986 16.95 4.46 4.461 11.236 5.653 16.95 2.986l84.854-39.599c3.111-1.452 3.623-2.354 14.148-12.748zm104.806-235.067-24.89-24.89 24.043-24.043 25.033 25.049z" />
                        <path d="m80.007 127h224c8.284 0 15-6.716 15-15s-6.716-15-15-15h-224c-8.284 0-15 6.716-15 15s6.716 15 15 15z" />
                        <path d="m80.007 207h176c8.284 0 15-6.716 15-15s-6.716-15-15-15h-176c-8.284 0-15 6.716-15 15s6.716 15 15 15z" />
                        <path d="m208.007 257h-128c-8.284 0-15 6.716-15 15s6.716 15 15 15h128c8.284 0 15-6.716 15-15s-6.716-15-15-15z" />
                      </g>
                    </svg>
                  </button>
                )}
              </div>

              <DropdownButton id="dropdown-basic-button" title={sopState}>
                <Dropdown.Item
                  onClick={() => {
                    setSopState("In Progress");
                  }}
                >
                  In Progress
                </Dropdown.Item>
                <Dropdown.Item
                  onClick={() => {
                    setSopState("Completed");
                  }}
                >
                  Completed
                </Dropdown.Item>
                <Dropdown.Item
                  onClick={() => {
                    setSopState("On Hold");
                  }}
                >
                  On Hold
                </Dropdown.Item>
                <Dropdown.Item
                  onClick={() => {
                    setSopState("Reassigned");
                  }}
                >
                  Reassigned
                </Dropdown.Item>
              </DropdownButton>
            </div>
          </Col>
        </Row>

        {addNoteVisibility && (
          <Row className="hbar-row">
            <Col lg="12">
              <textarea row="4" className="margin-top-20"></textarea>

              <div className="inline-row-right">
                <button
                  onClick={() => setAddNoteVisibility(false)}
                  className="action-button padding-left-5 padding-right-5"
                >
                  Save
                </button>
              </div>
            </Col>
          </Row>
        )}
      </div>
    );
  }

  function Operate(props) {
    const info = props.data;
    const id = props.id;
    console.log(id);

    return (
      <div className="hbar-list-item" {...props}>
        <Row className="hbar-row">
          <Col lg="7">{info.name}</Col>

          <Col lg="3">{info.agency}</Col>
          {status1 == "RCT" && id == "Building Security" && (
            <Col lg="2">{info.status}</Col>
          )}
          {status2 == "SPF" && id == "SPF/SCDF" && (
            <Col lg="2">{info.status}</Col>
          )}

          <div className="icon">
            <svg
              height="35"
              width="35"
              class="icon icon-chevron-right"
              viewBox="0 0 32 32"
              aria-hidden="true"
            >
              <path
                d="M18.629 15.997l-7.083-7.081L13.462 7l8.997 8.997L13.457 25l-1.916-1.916z"
                fill="white"
              />
            </svg>
          </div>
        </Row>
      </div>
    );
  }

  return (
    <div>
      <Card>
        <div className="operating-procedure">
          {sopForm.visible == false && (
            <div>
              <h1 className="content-title">
                Recommended Standard Operating Procedure
              </h1>

              <div className="header-info">
                <Row>
                  <Col lg="6" className="padding-left-40">
                    Standard Operating Procedure
                  </Col>
                  <Col lg="3" className="padding-left-60">
                    Agency
                  </Col>
                  <Col lg="3" className="padding-left-50">
                    Status
                  </Col>
                </Row>
              </div>
            </div>
          )}

          {sopForm.visible == true && (
            <div>
              <h1 className="content-title-active">
                <button
                  className=" padding-bottom-5 margin-right-15 "
                  onClick={() => {
                    setSopForm({ visible: false });
                  }}
                >
                  <Icons.Back height={24} width={22} />
                </button>

                {sopForm.name}
              </h1>

              <div className="header-info">
                <Row>
                  <Col lg="6" className="padding-left-40">
                    Standard Operating Procedure
                  </Col>
                  <Col lg="3" className="padding-left-40">
                    Execute
                  </Col>
                  <Col lg="3" className="padding-left-50">
                    Status
                  </Col>
                </Row>
              </div>
            </div>
          )}

          <SopCard />
        </div>
      </Card>
    </div>
  );
}

export default SopForm;
