import React from "react";
import { ProgressBar } from "react-bootstrap";
import Card from "../Components/card";
import Video from "../Components/video";
import CCTV1 from "../Assets/rct-entrance.mp4";
import CCTV2 from "../Assets/lobby.m4v";
import * as PropTypes from "prop-types";

export default function CCTVCard(progress) {
  return (
    <Card>
      <div className="activity-cctv">
        <h1 className="content-title">CCTV Footage</h1>
        <div className="content-log">
          {progress < 130 ? (
            <CCTVProgressBar now={progress} />
          ) : (
            <CCTVContent progress={progress} />
          )}
        </div>
      </div>
    </Card>
  );
}

function CCTVContent(props) {
  return (
    <>
      <div className="activity-cctv-content row margin-left-5">
        <div className="activity-cctv">
          <Video
            //vid={"https://youtu.be/PtvZWJC0omY"}
            vid={CCTV1}
            width={200}
            height={100}
            loop={true}
            muted={true}
          />
        </div>

        <div className="activity-cctv-lv">
          <div className="lv-button">
            <div>LV. 1</div>
          </div>
          <span className="lv-address">RCT Entrance</span>
        </div>
        <div>
          <span className="text-cctv">CCTV1</span>
        </div>
      </div>

      {props.progress < 200 ? (
        <></>
      ) : (
        <div
          // style={{ marginLeft: "5px", paddingTop: "50px" }}
          className="activity-cctv-content row margin-left-5 padding-top-50"
        >
          <div className="activity-cctv">
            <Video
              //vid={"https://youtu.be/S8Qpy2LTXlE"}
              vid={CCTV2}
              width={200}
              height={100}
              loop={true}
              muted={true}
            />
          </div>
          <div className="activity-cctv-lv">
            <div className="lv-button">
              <div>LV. 34</div>
            </div>
            <span className="lv-address">Lobby</span>
          </div>
          <div>
            <span className="text-cctv">CCTV2</span>
          </div>
        </div>
      )}
    </>
  );
}

function CCTVProgressBar(props) {
  return (
    <div className={"progress-bar-container"}>
      <div className={"progress-text"}> Connecting to local CCTVs...</div>
      <ProgressBar now={props.now} variant={"CCTV"} />
    </div>
  );
}

CCTVProgressBar.propTypes = { now: PropTypes.number };
