import React from "react";
import { Link } from "react-router-dom";
import Carousel from "./Carousel";
import Card from "./card";

function NEA_WeatherTracking(props) {
  return (
    <div class="social-media-tracking">
      <Card>
        <div className={"head-row"}>
          <h1 className="head">Weather Data </h1>
        </div>

        <div class="inner-card carousel-row">
          <Carousel clicked={props.clicked} />
        </div>
      </Card>
    </div>
  );
}

export default NEA_WeatherTracking;
