import React, { useState } from "react";
import { Link } from "react-router-dom";
function FloatingMenu() {
  const [exapndMenu, setExpandMenu] = useState(false);

  const OpenCloseMenu = ()=>{

    setExpandMenu(!exapndMenu);
  }
  return (
    <div className="floating-menu">
      {exapndMenu && (
        <div className="menu animate__animated animate__fadeIn">
          <div class="list-container">
            <div class="list-item">
              <Link to="/" onClick={OpenCloseMenu}>National Overview Page</Link>
            </div>

            <div class="list-item">
              <Link to="/incident-report"  onClick={OpenCloseMenu}>Incident Report</Link>
            </div>
            <div class="list-item">
              <Link to="/incident-threats"  onClick={OpenCloseMenu}>Incident Threats</Link>
            </div>

            <div class="list-item">
              <Link to="/incident-response"  onClick={OpenCloseMenu} >Incident Response</Link>
            </div>
            

            <div class="list-item">
              <Link to="/citizen-watch"  onClick={OpenCloseMenu} >Citizen Watch</Link>
            </div>
          </div>
        </div>
      )}

      <div
        className="main-button"
        onClick={OpenCloseMenu}
      >

  
        <div className={"arrow-up " + (exapndMenu ? "C4-icon-hide": "C4-icon-expand")}    />
      </div>
    </div>
  );
}

export default FloatingMenu;
