import React, { useState, useEffect } from "react";
import { Col, Row, Button } from "react-bootstrap";
import Card from "../Components/card";
import WidgetValue from "./widget-value";
import Spin from "./spinningAnimation";

function ResponderNearby(props) {
  const [clickedRequestCitizenResponders, setClickedRequestCitizenResponders] =
    useState(false);
  const [initiateBlink, setInitiateBlink] = useState(false);
  const [stopTime, setStopTime] = useState(false);
  const [clickedSentRequest, SetClickedSentRequest] = useState(false);
  const [loadComplete, SetLoadComplete] = useState(false);
  const initiateNum = [3, 5, 5];
  const [CPRAEDNum, setCPRAEDNum] = useState(1);
  const [basicFireNum, setBasicFireNum] = useState(0);
  const [basicFirstAidNum, setBasicFirstAidNum] = useState(0);
  const [williamInitiateStatus, setWilliamStatus] = useState("Viewed");
  const [patriciaInitiateStatus, setPatriciaStatus] = useState("Viewed");
  const [amirulInitiateStatus, setAmirulStatus] = useState("Viewed");
  const [containerHeight, setContainerHeight] = useState("responderNearby");

  const incNum = (name) => {
    if (name === "cpfaed") {
      if (CPRAEDNum < initiateNum[0]) {
        setCPRAEDNum(CPRAEDNum + 1);
      }
    } else if (name === "basicfire") {
      if (basicFireNum < initiateNum[1]) {
        setBasicFireNum(basicFireNum + 1);
      }
    } else if (name === "firstaid") {
      if (basicFirstAidNum < initiateNum[2]) {
        setBasicFirstAidNum(basicFirstAidNum + 1);
      }
    }
  };

  const decNum = (name) => {
    if (name === "cpfaed") {
      if (CPRAEDNum > 0) {
        setCPRAEDNum(CPRAEDNum - 1);
      } else {
        setCPRAEDNum(0);
      }
    } else if (name === "basicfire") {
      if (basicFireNum > 0) {
        setBasicFireNum(basicFireNum - 1);
      } else {
        setBasicFireNum(0);
      }
    } else if (name === "firstaid") {
      if (basicFirstAidNum > 0) {
        setBasicFirstAidNum(basicFirstAidNum - 1);
      } else {
        setBasicFirstAidNum(0);
      }
    }
  };

  const onClickedRequestCitizenResponders = () => {
    if (props.enableResponderButton === true) {
      setClickedRequestCitizenResponders(true);
      setInitiateBlink(true);
      props.callAssetManagement("requestClicked");
    }
    setContainerHeight("responderNearby responderNearby-height");
  };

  const onClickedSentRequest = () => {
    SetClickedSentRequest(true);

    setTimeout(() => {
      SetLoadComplete(true);
    }, 1500);
    props.CPRRequest(true);
  };

  const LoadingRequest = () => {
    return (
      <div>
        <div className={"header-info margin-left-35"}>
          <div className={"row justify-content-center"}>
            <div className={"margin-top-40 margin-bottom-20"}>
              <div className="padding-bottom-20 centered">
                <div className="human-img" />
                <Spin />
              </div>
              <div className={"text label"}>Sending request to responders</div>
            </div>
          </div>
        </div>
      </div>
    );
  };

  const onEdit = () => {
    SetClickedSentRequest(false);
  };

  const stopTimer = (timer) => {
    console.log("timer stoped");
    // clearInterval(timer);
    clearTimeout(timer);
    setStopTime(true);
    props.sendCall("clickMapForArrival");
  };

  const StatusTimer = () => {
    const timer = setTimeout(() => {
      if (williamInitiateStatus === "Viewed") {
        setWilliamStatus("Received");
      } else if (williamInitiateStatus === "Received") {
        setWilliamStatus("Accepted");
        props.sendCall("williamAccept");
        setPatriciaStatus("Received");
      } else if (
        williamInitiateStatus === "Accepted" &&
        patriciaInitiateStatus === "Received"
      ) {
        setPatriciaStatus("Accepted");
        props.sendCall("patriciaAccept");
        setAmirulStatus("Received");
      } else if (
        williamInitiateStatus === "Accepted" &&
        patriciaInitiateStatus === "Accepted" &&
        amirulInitiateStatus === "Received"
      ) {
        props.sendCall("amirulAccept");
        setAmirulStatus("Accepted");
      }
      if (
        williamInitiateStatus === "Accepted" &&
        patriciaInitiateStatus === "Accepted" &&
        amirulInitiateStatus === "Accepted"
      ) {
        stopTimer(timer);
      }
    }, 3000);
  };

  if (loadComplete) {
    if (!stopTime) {
      StatusTimer();
    }
  }

  if (props.enableResponderArrival) {
    if (williamInitiateStatus === "Accepted") {
      setWilliamStatus("Arrived");
    }
    if (patriciaInitiateStatus === "Accepted") {
      setPatriciaStatus("On-hold");
    }
    if (amirulInitiateStatus === "Accepted") {
      setAmirulStatus("On-hold");
    }
  }

  return (
    <Card>
      <div class={containerHeight}>
        <div className={"responderNearby-head-row "}>
          <h1 className={"content-title"}>
            Available Responders
            <span className={"responderNearby-distance float-right"}>
              within 400 meters
            </span>{" "}
          </h1>
        </div>

        <div class="responderNearby-content inner-card">
          <Row>
            <Col lg={5}>
              <WidgetValue
                startvalue={1}
                duration={8}
                value={6}
                decimals={0}
                fontsize={"large"}
                title={"Total responders"}
              />
            </Col>
            <Col lg={6} className={"padding-0"}>
              <div className={"training-title"}>Trainings</div>
              <div className={"block gray"}>
                <span className={"cpr-icon"}></span>CPR/AED Trained
                <div className={"responders"}>
                  <span className={"number"}>{initiateNum[0]}</span>
                  {props.enableIconBlink === true ? (
                    initiateBlink === false ? (
                      <span className={"alert-circle-icon blink"}></span>
                    ) : (
                      <span className={"alert-circle-icon "}></span>
                    )
                  ) : (
                    <span className={"alert-circle-icon "}></span>
                  )}
                </div>
              </div>
              <div className={"block"}>
                <span className={"basic-fire-icon"}></span>Basic Fire Fighting
                <div className={"responders"}>
                  <span className={"number"}>{initiateNum[1]}</span>
                  <span className={"alert-circle-icon hidden"}></span>
                </div>
              </div>
              <div className={"block gray"}>
                <span className={"first-aid-icon"}></span>Basic First Aid
                <div className={"responders"}>
                  <span className={"number"}>{initiateNum[2]}</span>
                  <span className={"alert-circle-icon hidden"}></span>
                </div>
              </div>
            </Col>
          </Row>
          <Row className={"margin-0"}>
            {props.enableResponder == true &&
              (clickedRequestCitizenResponders === true ? (
                <div className={"responderBottomBox"}>
                  {clickedSentRequest === false ? (
                    <div className={"responderModal "}>
                      <span className={"responderModalTitle"}>
                        Request for community responders
                      </span>
                      <div className={"responderModalForm"}>
                        <Row>
                          <Col lg={7}>
                            <div className={"text-counter"}>
                              CPR-AED trained ({initiateNum[0]})
                            </div>
                            <span
                              className={"alert-circle-secondary-icon "}
                            ></span>
                            <span className={"alertMessage"}>
                              1 responder recommended
                            </span>
                          </Col>
                          <Col className={"plusminusform"}>
                            <Button
                              type="button"
                              className={"minus"}
                              onClick={() => decNum("cpfaed")}
                            >
                              <div className={"minus-icon"}></div>
                            </Button>
                            <div className={"count-num"}>
                              <div className={"num"}>{CPRAEDNum}</div>
                            </div>
                            <Button
                              type="button"
                              className={"plus"}
                              onClick={() => incNum("cpfaed")}
                            >
                              <div className={"plus-icon"}></div>
                            </Button>
                          </Col>
                        </Row>
                        <Row>
                          <Col lg={7}>
                            <div className={"text-counter"}>
                              Basic Fire Fighting trained ({initiateNum[1]})
                            </div>
                          </Col>
                          <Col className={"plusminusform"}>
                            <Button
                              type="button"
                              className={"minus"}
                              onClick={() => decNum("basicfire")}
                            >
                              <div className={"minus-icon"}></div>
                            </Button>
                            <div className={"count-num"}>
                              <div className={"num"}>{basicFireNum}</div>
                            </div>
                            <Button
                              type="button"
                              className={"plus"}
                              onClick={() => incNum("basicfire")}
                            >
                              <div className={"plus-icon"}></div>
                            </Button>
                          </Col>
                        </Row>
                        <Row>
                          <Col lg={7}>
                            <div className={"text-counter"}>
                              Basic First Aid trained ({initiateNum[2]})
                            </div>
                          </Col>
                          <Col className={"plusminusform"}>
                            <Button
                              type="button"
                              className={"minus"}
                              onClick={() => decNum("firstaid")}
                            >
                              <div className={"minus-icon"}></div>
                            </Button>
                            <div className={"count-num"}>
                              <div className={"num"}>{basicFirstAidNum}</div>
                            </div>
                            <Button
                              type="button"
                              className={"plus"}
                              onClick={() => incNum("firstaid")}
                            >
                              <div className={"plus-icon"}></div>
                            </Button>
                          </Col>
                        </Row>
                        <Row>
                          <button
                            className={"sendRequestCR-button"}
                            onClick={() => onClickedSentRequest()}
                          >
                            Send Request
                          </button>
                        </Row>
                      </div>
                    </div>
                  ) : loadComplete === false ? (
                    <LoadingRequest />
                  ) : (
                    <div className={"responderModal "}>
                      <span className={"responderModalTitle"}>
                        Requested community responder
                      </span>
                      <Button
                        type="button"
                        onClick={onEdit}
                        className={"edit-button"}
                      >
                        <span className={"edit-pen"}></span>
                        Edit Details
                      </Button>
                      <div className={"responderModalAccept"}>
                        <span className={"requestedTitle"}>
                          CPR-AED Trained
                        </span>
                        <span>({CPRAEDNum} responder)</span>
                        <div className={"responderTable"}>
                          <Row className={"responder-title-row"}>
                            <Col>
                              <span className={"text value"}>Responder</span>
                            </Col>
                            <Col>
                              <span className={"text value"}>Status</span>
                            </Col>
                          </Row>
                          <Row className={"responderTable-row"}>
                            <Col>
                              <span className={"william-ng-icon"}></span>
                              <span className={"responder-name"}>
                                William Ng
                              </span>
                            </Col>
                            <Col>
                              <span className={williamInitiateStatus}>
                                <span className={"icon"}></span>
                                {williamInitiateStatus}
                              </span>
                            </Col>
                          </Row>
                          <Row className={"responderTable-row"}>
                            <Col>
                              <span className={"patricia-lim-icon"}></span>
                              <span className={"responder-name"}>
                                Patricia Lim
                              </span>
                            </Col>
                            <Col>
                              <span className={patriciaInitiateStatus}>
                                <span className={"icon"}></span>{" "}
                                {patriciaInitiateStatus}
                              </span>
                            </Col>
                          </Row>
                          <Row className={"responderTable-row"}>
                            <Col>
                              <span className={"amirul-faiz-icon"}></span>
                              <span className={"responder-name"}>
                                Amirul Faiz
                              </span>
                            </Col>
                            <Col>
                              <span className={amirulInitiateStatus}>
                                <span className={"icon"}></span>
                                {amirulInitiateStatus}
                              </span>
                            </Col>
                          </Row>
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              ) : (
                <button
                  className={
                    props.enableResponderButton === true
                      ? "requestCR-button"
                      : "requestCR-button button-disabled"
                  }
                  onClick={() => onClickedRequestCitizenResponders()}
                >
                  Request community responders
                </button>
              ))}
          </Row>
        </div>
      </div>
    </Card>
  );
}

export default ResponderNearby;
