import * as React from "react";
import { useState, useRef } from "react";
import Card from "../Components/card";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhoneAlt } from "@fortawesome/free-solid-svg-icons";
import { Row, Button } from "react-bootstrap";
import Video from "./video";
import videoStream from "../Assets/calling-video.mp4";
import Constants from "../Constants";
import { height } from "dom7";

export default function CitizenWatchCallCard(props) {
  const PulseButton = (props) => {
    return (
      <div className="ring-con">
        <div className="ringring" />
        <div className="ringring" />
        <div className="ringring" />
        <Button className="circle " onClick={props.onClick}>
          <Row className={"ml-3"}>
            <FontAwesomeIcon
              icon={faPhoneAlt}
              inverse
              className={"pulse-icon"}
            />
            <div className={"button-text"}>Answer Call</div>
          </Row>
        </Button>
      </div>
    );
  };

  const PulseCall = (props) => {
    return (
      <div onClick={props.onClick}>
        <div className="call-pulse" />
        <div className="call-pulse" />
        <div className="call-pulse" />
        <div className="call-active-icon" />
      </div>
    );
  };

  const [screen, setScreen] = useState(props.Screen);
  const [endVideoCall, setEndVideoCall] = useState(false);
  const onAnswerCall = () => {
    setScreen(Constants.callState[1]);
    props.onStartCall();
    handleStart();
  };
  const switchVideo = () => {
    setScreen(Constants.callState[2]);
    props.sentVideoRequest(true);
    props.switchSOP(true);
    setTimeout(() => {
      setEndVideoCall(true);
    }, 33000);
  };
  const switchCall = () => {
    setScreen(Constants.callState[3]);
    props.sentVideoRequest(true);
  };

  const requestVideo = () => {
    switchVideo();
    props.onVideocall(true);
  };

  const formatTime = (timer) => {
    const getSeconds = `0${timer % 60}`.slice(-2);
    const minutes = `${Math.floor(timer / 60)}`;
    const getMinutes = `0${minutes % 60}`.slice(-2);
    const getHours = `0${Math.floor(timer / 3600)}`.slice(-2);

    return `${getHours} : ${getMinutes} : ${getSeconds}`;
  };

  const useTimer = (initialState = 0) => {
    const [timer, setTimer] = useState(initialState);
    const countRef = useRef(null);

    const handleStart = () => {
      countRef.current = setInterval(() => {
        setTimer((timer) => timer + 1);
      }, 1000);
    };

    if (props.endCall === true) {
      clearInterval(countRef.current);
    }

    return {
      timer,
      handleStart,
    };
  };

  const { timer, handleStart } = useTimer(0);
  //for end call component//
  const Timer = formatTime(timer);
  const split = Timer.split(":");
  const hour = split[0];
  const mins = split[1];
  const secs = split[2];

  const initialScreen = (
    <Row className="padding-bottom-20">
      <div className={"col-md-12"}>
        <div className="person-icon" />
        <div className={"white-text-1"}>8111 1111</div>
        <div className="row padding-left-75 padding-right-75 padding-top-20">
          <div className="divert-img" />
          <PulseButton onClick={onAnswerCall} />
        </div>
        <div className="icon-container padding-top-10"></div>
      </div>
    </Row>
  );

  const defaultScreen = (
    <Row className="padding-bottom-20">
      <div className={"col-md-8 offset-md-2"}>
        <div className="white-text-small margin-top-20">Sentiment Analysis</div>
        {props.sentiment == "Anxious" ? (
          <div>
            <div className={"white-text-1"}>Generally Anxious</div>
            <div className="anxious-bar" />
          </div>
        ) : (
          <div>
            <div className={"white-text-1"}>Generally Neutral</div>
            <div className="nuetral-bar" />
          </div>
        )}

        <div className="icon-container padding-top-20">
          <div className="mic-icon" />
          <div className="video-call-icon" onClick={switchCall} />
          <div className="call-icon" />
        </div>
        <div className="icon-container padding-top-10">
          <div className="shift-left white-text-small">Mute my mic</div>
          <div className="white-text-small shift-left">Request video</div>
          <div className="white-text-small ">Cancel</div>
        </div>
      </div>
    </Row>
  );
  const callingScreen = (
    <Row className="padding-bottom-20">
      <div className={"col-md-8 offset-md-2"}>
        <div className="white-text-small margin-top-20">Sentiment Analysis</div>
        {props.sentiment == "Anxious" ? (
          <div>
            <div className={"white-text-1"}>Generally Anxious</div>
            <div className="anxious-bar" />
          </div>
        ) : (
          <div>
            <div className={"white-text-1"}>Generally Neutral</div>
            <div className="nuetral-bar" />
          </div>
        )}
        <div className="icon-container padding-top-20">
          <div className="mic-icon" />
          {/* <div className="call-active-icon" /> */}
          <PulseCall onClick={requestVideo} />
          <div className="call-icon" />
          {/* <PulseButton onClick={onClick} /> */}
        </div>
        <div className="icon-container padding-top-10">
          <div className="white-text-small shift-left">Mute my mic</div>
          <div className="white-text-small shift-left">Request video</div>
          <div className="white-text-small ">Cancel</div>
        </div>
      </div>
    </Row>
  );

  const videoScreen = (
    <div>
      {endVideoCall === true ? (
        <div className="justify-content-center d-flex">
          <div className="endVideCall-icon" />
        </div>
      ) : (
        <div>
          <Video
            vid={videoStream}
            width={433}
            height={650}
            loop={false}
            muted={false}
          />
          <div className="caller-container">
            <div className="caller-text">8111 1111</div>
          </div>
        </div>
      )}
      <div className="mic-icon-1" />
      <div className="call-icon-1" />
    </div>
  );

  const endCallScreen = (
    <Row className="padding-bottom-20">
      <div className={"col-md-8 offset-md-2 margin-top-50"}>
        {/* <div className="person-icon" /> */}
        <div className={"white-text-1"}>Call Ended</div>
        <div className={"white-text-medium"}>
          {mins} mins {secs} secs
        </div>
        <div className={"white-text-medium margin-top-10"}>8111 1111</div>
        <div className="justify-content-center d-flex padding-top-20">
          <div className="recall-icon" />
        </div>
        <div className="justify-content-center padding-top-10">
          <div className="white-text-small">Call Again</div>
        </div>
      </div>
    </Row>
  );

  return (
    <Card>
      <div
        className={
          screen === "onVideo"
            ? "citizenWatchCallBox-video"
            : "citizenWatchCallBox"
        }
      >
        <div className="citizen-watch-phone ">
          <h1 className="content-title">
            Caller
            {/* <span className="float-right">00:03</span> */}
            <span className="float-right">
              <p>{formatTime(timer)}</p>
            </span>
          </h1>
        </div>

        {props.endCall === true ? (
          endCallScreen
        ) : (
          <div>
            {screen === "initial" && initialScreen}
            {screen === "calling" && defaultScreen}
            {screen === "onVideo" && videoScreen}
            {screen === "onCall" && callingScreen}
          </div>
        )}
      </div>
    </Card>
  );
}

function callerDisplay(props) {
  return (
    <div className="caller-container">
      <div className="caller-text">{props.id}</div>
    </div>
  );
}
