import React from "react";
import { Row, Col, Button } from "react-bootstrap";
import Constants from "../../Constants";

const SelectRate = (props) => {
  return (
    <div className={"select-rate margin-top-20"}>
      <Row>
        <Col lg={3} className={"padding-left-40"}>
          <div className={"text"}>Select severity rating:</div>
        </Col>
        <Col lg={9}>
          <div className={"row incident-button"}>
            <Button
              value="Life-threatening"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton ===
                Constants.severityType.LIFETHREATENING
                  ? "btn-rate-active btn-danger"
                  : "btn-rate btn-danger"
              }
              onClick={props.handleChange}
            >
              Life-threatening
            </Button>
            <Button
              value="Emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton ===
                Constants.severityType.EMERGENCY
                  ? "btn-rate-active btn-warning"
                  : "btn-rate btn-warning"
              }
              onClick={props.handleChange}
            >
              Emergency
            </Button>
            <Button
              value="Minor emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton ===
                Constants.severityType.MINOREMERGENCY
                  ? "btn-rate-active btn-info"
                  : "btn-rate btn-info"
              }
              onClick={props.handleChange}
            >
              Minor emergency
            </Button>
            <Button
              value="Non-emergency"
              name="ratebutton"
              className={
                props.buttonChange.ratebutton ===
                Constants.severityType.NONEMERGENCY
                  ? "btn-rate-active btn-success"
                  : "btn-rate btn-success"
              }
              onClick={props.handleChange}
            >
              Non-emergency
            </Button>
          </div>
        </Col>
      </Row>
      <Row>
        <Col lg={3}></Col>
        <Col lg={9} className={"padding-0"}>
          {props.buttonChange.ratebutton ===
            Constants.severityType.LIFETHREATENING && (
            <div className={"margin-top-20"}>
              <span class={"alert-circle-secondary-icon"}></span>
              <span className={"alertMessage"}>
                EMS dispatch required. Closest location is Yishun Fire Station
              </span>
            </div>
          )}
        </Col>
      </Row>
    </div>
  );
};
export default SelectRate;
