import React from "react";
import Typing from "react-typing-animation";

const Type = React.memo((props) => {
  return (
    // <Typed strings={[props.text]} typeSpeed={30} className = {props.className}/>
    <Typing speed={10}>
      <div>
        {props.text.map((val) => (
          <p className="activity-log-time">
            {val.time}
            <br />
            <span className="activity-log-desc">
              {val.content}
              <br />
            </span>
            <br />
            <Typing.Delay ms={1000} />
          </p>
        ))}
      </div>
    </Typing>
  );
});
export default Type;
