import React, { useState } from "react";
import Card from "../Components/card";
import WidgetValue from "./widget-value";
import { Col, Row } from "react-bootstrap";
import {
  Accordion,
  AccordionItem,
  AccordionItemHeading,
  AccordionItemButton,
  AccordionItemPanel,
} from "react-accessible-accordion";

function AssetManagement(props) {
  const [toggle, setToggle] = useState(true);

  const onClickShowMap = () => {
    setToggle(true);
  };

  const onClickCloseMap = () => {
    setToggle(false);
  };

  if (
    props.assetManagementHeight ===
    "assetManagementMap-content-secondary inner-card"
  ) {
    if (toggle) {
      setToggle(false);
    }
  }
  return (
    <Card>
      <div className={"assetManagementBox"}>
        <h1 className="content-title">Asset Management </h1>
        {props.assetContent == false ? (
          <div
            className={
              props.enableResponder == false
                ? "assetManagementBox-content inner-card"
                : "assetManagementBox-modified inner-card"
            }
          >
            <Accordion allowZeroExpanded>
              <AccordionItem>
                <AccordionItemHeading>
                  <AccordionItemButton>
                    <div className={"title-row"}>
                      <span className={"title "}>Khoo Teck Puat Hospital</span>
                      <span className={"text value distance"}>
                        10 mins away
                      </span>
                    </div>
                    <div>
                      <span className={"person-logo"}></span>
                      <span className={"assetNumber"}>297</span>
                    </div>
                  </AccordionItemButton>
                </AccordionItemHeading>
                <AccordionItemPanel>
                  <div className={"text value head"}>
                    Relevant capabilities:
                  </div>
                  <div className={"panel-block gray"}>
                    <span className={"title"}>Burns Centre</span>
                    <span className={"capacity"}>FULL</span>
                  </div>
                  <div className={"panel-block "}>
                    <span className={"title"}>Cardiac Centre</span>
                    <span className={"capacity"}>
                      10 patients to full capacity
                    </span>
                  </div>
                  <div className={"panel-block gray"}>
                    <span className={"title"}>Trauma Centre</span>
                    <span className={"capacity"}>
                      7 patients to full capacity
                    </span>
                  </div>
                </AccordionItemPanel>
              </AccordionItem>
              <AccordionItem>
                <AccordionItemHeading>
                  <AccordionItemButton>
                    <div className={"title-row"}>
                      <span className={"title"}>Yishun Police Station</span>
                      <span className={"text value distance"}>8 mins away</span>
                    </div>
                    <div>
                      {" "}
                      <span className={"person-logo"}></span>
                      <span className={"assetNumber"}>56</span>
                      <span className={"officer-logo-home"}></span>
                      <span className={"text label"}>
                        Officer in-charge{" "}
                        <span className={"text value"}>
                          &nbsp;&nbsp;INSP Philip Neo
                        </span>
                      </span>
                    </div>
                  </AccordionItemButton>
                </AccordionItemHeading>
                <AccordionItemPanel>
                  <div className={"text value head"}>
                    Specialists units available:
                  </div>
                  <div className="list-box ">
                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={24}
                        decimals={0}
                        fontsize={"large"}
                        title={"Community Police"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={12}
                        decimals={0}
                        fontsize={"large"}
                        title={"Ground Response Force"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={12}
                        decimals={0}
                        fontsize={"large"}
                        title={"Frontline Police"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={8}
                        decimals={0}
                        fontsize={"large"}
                        title={"Traffic Police"}
                      />
                    </div>
                  </div>
                </AccordionItemPanel>
              </AccordionItem>
              <AccordionItem>
                <AccordionItemHeading>
                  <AccordionItemButton>
                    <div className={"title-row"}>
                      <span className={"title"}>Yishun Fire Station</span>
                      <span className={"text value distance"}>8 mins away</span>
                    </div>
                    <div>
                      <span className={"person-logo"}></span>
                      <span className={"assetNumber"}>68</span>
                      <span className={"officer-logo-home"}></span>

                      <span className={"text label"}>
                        Officer in-charge{" "}
                        <span className={"text value"}>
                          &nbsp;&nbsp;CPT Sanjay Nair
                        </span>
                      </span>
                    </div>
                  </AccordionItemButton>
                </AccordionItemHeading>
                <AccordionItemPanel>
                  <div className={"text value head"}>
                    Specialists units available:
                  </div>
                  <div className="list-box ">
                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={24}
                        decimals={0}
                        fontsize={"large"}
                        title={"Paramedic"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={12}
                        decimals={0}
                        fontsize={"large"}
                        title={"DART"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={22}
                        decimals={0}
                        fontsize={"large"}
                        title={"Fire and rescue"}
                      />
                    </div>

                    <div className="box-value">
                      <WidgetValue
                        startvalue={1}
                        duration={8}
                        value={10}
                        decimals={0}
                        fontsize={"large"}
                        title={"Hazmat"}
                      />
                    </div>
                  </div>
                </AccordionItemPanel>
              </AccordionItem>
            </Accordion>
          </div>
        ) : (
          <div className={props.assetManagementHeight}>
            {props.enableResponderArrived == false ? (
              <div className={"text-center"}>
                <div className={"text-center"}>
                  Emergency Medical Service arriving in
                </div>

                <div className={"time-text"}>7mins 58 secs</div>
              </div>
            ) : (
              <div className={"text-center"}>
                <div className={"time-text margin-top-20"}>EMS has arrived</div>
              </div>
            )}
            <div className={"row margin-top-20"}>
              <div className={"col-6 text-left"}>Origin</div>
              <div className={"col-6 text-right"}>Destination</div>
            </div>
            <div className={"row"}>
              <div className={"col-5 text-left content-text"}>
                Yishun Fire Station
              </div>
              <div className={"col-2 text-center"}>
                <svg
                  width="16"
                  height="16"
                  viewBox="0 0 16 16"
                  fill="none"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    d="M0 7.00008V9.00008H12L6.5 14.5001L7.92 15.9201L15.84 8.00008L7.92 0.0800781L6.5 1.50008L12 7.00008H0Z"
                    fill="#177DDC"
                  />
                </svg>
              </div>
              <div className={"col-5 text-right content-text"}>
                Yishun St. 81
              </div>
            </div>
            <div className={"row margin-0 margin-top-10"}>
              {toggle === false ? (
                <div className={"toggleShowMap"} onClick={onClickShowMap}>
                  SHOW MAP
                </div>
              ) : (
                <div className={"asset-map"} onClick={onClickCloseMap}></div>
              )}
            </div>
            <div className={"row margin-0 margin-top-10"}>
              <div>EMS details</div>
            </div>
            <div className={"row margin-top-10"}>
              <div className={"col-6"}>
                <div className={"content-text"}>Plate no.: QX 858P</div>
                <div className={"content-text"}>POC: SGT Ken Chan</div>
              </div>
              <div className={"col-6"}>
                <div className={"asset-call"}></div>
              </div>
            </div>
          </div>
        )}
      </div>
    </Card>
  );
}

export default AssetManagement;
