export function IncomingMapData(props) {
  let history = useHistory();
  let [clicked, setClicked] = React.useState(false);

  function onClickedMap() {
    setClicked(true);
    props.sendCall(true);
  }

  function onClickedMarker() {
    history.push("/incident-report");
  }
  return (
    <div class="main-map">
      <div class="clickableMap" onClick={() => onClickedMap()}>
        {/* <span className={"text label"}>CLICK HERE</span> */}
      </div>
      {clicked === true && (
        <div>
          <div className={"fireMarker-title"}></div>
          <div className={"fireMarker"} onClick={onClickedMarker}></div>
        </div>
      )}
    </div>
  );
}
