import React, { useState } from "react";
import { Row, Col } from "react-bootstrap";
import IncidentHeader from "../Components/IncidentHeader";
import ActivityLog from "../Components/ActivityLogCard";
import DeploymentBar from "../Components/deployment";
import SopForm from "../Components/SopForm";
import Asset_Hospital from "../Components/Asset_HospitalCard";
import Evacuation from "../Components/EvacuationCard";
import CitizenResponders from "../Components/CitizenRespondersCard";

import { RightScreenState } from "../Atom";
import { useRecoilState } from "recoil";
import { useLocation } from "react-router-dom";

import ScreenControl from "../screencontrol";
import Constants from "../Constants";

function IncidentResponse() {
  const [toggleState, setToggleState] = useState(1);

  const toggleTab = (index) => {
    setToggleState(index);
  };

  const [mapUpdate, setMapUpdate] = useState(false);
  const onChangeMap = (value) => {
    setMapUpdate(value);
  };

  const location = useLocation();
  const [rightScreen, setRightScreen] = useRecoilState(RightScreenState);
  if (location.pathname === "/incident-response") {
    ScreenControl(Constants.videoType.c4News);
    setRightScreen(Constants.videoType.rapidDeploy);
  }

  return (
    <div>
      <IncidentHeader />

      {/* each card container space */}

      <DeploymentBar launchname="LAUNCH DISPATCH" launchroute="rapiddeploy" />
      <div className="padding-bottom-20" />

      <div className={"container-dashboard"}>
        <Row>
          <Col className="col-3 padding-bottom-20">
            <ActivityLog />
            <div className="padding-top-20"></div>
            <Evacuation />
          </Col>

          <Col className="col-6">
            {mapUpdate == false && <div className="sop-map"></div>}
            {mapUpdate == true && <div className="sop-updateMap"></div>}
            <div className="padding-top-20"></div>

            <SopForm onChangeMap={onChangeMap} />
          </Col>
          <Col className="col-3">
            {Asset_Hospital(toggleState, toggleTab)}
            {/* each card container space */}
            <div className="padding-top-20"></div>
            <CitizenResponders />
          </Col>
        </Row>
      </div>

      {/* rapid deploy video...
      <RapidDeploy />
  */}
    </div>
  );
}

export default IncidentResponse;
