import React from "react";

import { Row, Col } from "react-bootstrap";

import WidgetValue from "../widget-value";

const SummaryWidget = () => {
  return (
    <Row className={"margin-top-20"}>
      <Col lg={8}>
        <Row className={"padding-left-20 padding-bottom-20"}>
          <Col lg={4}>
            <div className="box-value">
              <WidgetValue
                value={"1"}
                prefix={""}
                title={"EMS team responded"}
                startvalue={3.12}
                duration={3}
                decimals={0}
                fontsize={"medium"}
              />
            </div>
          </Col>
          <Col lg={4}>
            <div className="box-value">
              <WidgetValue
                value={"1"}
                prefix={""}
                title={"Community Responder"}
                startvalue={3.12}
                duration={3}
                decimals={0}
                fontsize={"medium"}
              />
            </div>
          </Col>
          <Col lg={4}>
            <div className="box-value">
              <WidgetValue
                value={"9"}
                prefix={"mins"}
                title={"Time elapsed"}
                startvalue={3.12}
                duration={3}
                decimals={0}
                fontsize={"medium"}
              />
            </div>
          </Col>
        </Row>
      </Col>
      <Col lg={4}>
        <Row className={"margin-0"}>
          <Col lg={4}>
            <div className="box-value">
              <div className="stat-box row">
                <div className="scan-text">
                  Scan to view the detailed report
                </div>
              </div>
            </div>
          </Col>

          <div className="code-img"></div>
        </Row>
      </Col>
    </Row>
  );
};
export default SummaryWidget;
